<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                :deletable=!isRead
                connectable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5099F7',
          fill: '#5099F7',
          'fill-opacity': 1,
          r: '1', 'z-index': '998'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Command >>'">
                </text-element>
                <image-element
                        v-if="(Object.keys(value.aggregate).length === 0 && JSON.stringify(value.aggregate) === JSON.stringify({})) || value.name.length < 1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>

        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'Command'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/command.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->


        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'Command'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/command.png'"
        >
        </modeling-property-panel>


    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');

    var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'command-definition',
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Command'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',

                    aggregate: {},
                    boundedContext: {},

                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({}),
                        'z-index': 999
                    },
                    // checkValue: false,
                    //new pannel
                    isRestRepository: false,
                    controllerInfo: {
                        apiPath: '',
                        method: 'GET'
                    },
                    restRepositoryInfo: {
                        method: 'GET'
                    },
                    relationEventInfo: [],

                    // drawer: false,
                    // selected: false,
                    editing: false,
                }
            }
        },
        data: function () {
            return {
                isRead:false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }

                me.isRead = designer.isRead
            })
        },
        watch: {
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
            },
            // "value.trigger": function () {
            //     this.checkValue = true
            // },
            // "value.fieldDescriptors": function () {
            //     this.checkValue = true
            // },
            "value.boundedContext": function () {
                this.checkValue = true
            },
            // "namePanel": function () {
            //     this.checkValue = true
            // },
            // "value.name": function () {
            //     this.checkValue = true
            // },
            // "value.controllerInfo":{
            //     deep:true,
            //     handler: function () {
            //         this.value.checkValue = true
            //     }
            // },
            // "value.isRestRepository":{
            //     deep:true,
            //         handler: function () {
            //         this.checkValue = true
            //     }
            // },
            "value.relationEventInfo": {
                deep: true,
                handler: function () {
                    this.checkValue = true
                }
            },
            "value.aggregate": {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    if (Object.keys(newVal).length == 0) {
                        //delete agg
                    } else {
                        if (newVal.id == oldVal.id) {
                            // console.log("Equal")
                        } else {
                            // console.log("no Equal")
                            var addObj = {
                                state: "addCommand",
                                element: JSON.parse(JSON.stringify(me.value))
                            }
                            var deleteObj = {
                                state: "deleteCommand",
                                element: JSON.parse(JSON.stringify(me.value))
                            }
                            if (oldVal.id != null) {
                                me.$EventBus.$emit(oldVal.id, deleteObj)
                            }
                            me.$EventBus.$emit(newVal.id, addObj)

                            this.checkValue = true
                        }
                    }
                }
            },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {

                    // me.setNameCase(me.namePanel)

                    me.$nextTick(function () {
                        var obj = {
                            state: "updateCommand",
                            element: JSON.parse(JSON.stringify(me.value))
                        }

                        //update agg
                        if (Object.keys(me.value.aggregate).length != 0) {
                            me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                        }

                        //update conn Event
                        if (me.value.relationEventInfo.length > 0) {
                            me.value.relationEventInfo.forEach(function (policy) {
                                me.$EventBus.$emit(policy.eventValue.elementView.id, obj)
                            })
                        }
                        me.checkValue = false
                    })

                }

            },
            // "value.drawer": _.debounce(
            //     function (newVal, oldVal) {
            //         var me = this
            //         if (newVal == false) {
            //
            //             if( me.value.checkValue){
            //                 var obj = {
            //                     state: "updateCommand",
            //                     element: JSON.parse(JSON.stringify(me.value))
            //                 }
            //
            //                 if (Object.keys(me.value.aggregate).length != 0) {
            //                     me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
            //                 }
            //
            //                 me.value.checkValue = false
            //             }
            //             // if ( ! me.value.isRestRepository) {
            //             //     var obj = {
            //             //         state: "deleteCommand",
            //             //         element: JSON.parse(JSON.stringify(me.value))
            //             //     }
            //             //     if (Object.keys(me.value.restRepositoryInfo.aggregate).length != 0) {
            //             //         me.$EventBus.$emit(`${me.value.restRepositoryInfo.aggregate.id}`, obj)
            //             //     }
            //             //
            //             // } else {
            //             //
            //             //     var obj = {
            //             //         state: "updateCommand",
            //             //         element: JSON.parse(JSON.stringify(me.value))
            //             //     }
            //             //
            //             //     if (Object.keys(me.value.restRepositoryInfo.aggregate).length != 0) {
            //             //         me.$EventBus.$emit(`${me.value.restRepositoryInfo.aggregate.id}`, obj)
            //             //     }
            //             //
            //             // }
            //
            //         }
            //     }, 200)

        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {


                if (obj.state == 'updateEvent') {
                    console.log(obj)
                    me.value.relationEventInfo.forEach(function (eventItem, evtIdx) {
                        if (eventItem.eventValue.elementView.id == obj.element.elementView.id) {
                            me.value.relationEventInfo[evtIdx].eventValue = JSON.parse(JSON.stringify(obj.element));
                        }
                    })

                }
                if (obj.state == "deleteAggregate") {
                    me.value.aggregate = {}

                    // if (obj.type == "org.uengine.modeling.model.Aggregate") {
                    //     me.value.aggregate = {}
                    // }
                } else if (obj.state == 'updateAggregate') {

                    var tmpAgg = {
                        'id': obj.element.elementView.id,
                        'name': obj.element.name,
                        'namePlural': obj.element.namePlural,
                        'namePascalCase': obj.element.namePascalCase,
                        'nameCamelCase': obj.element.nameCamelCase,
                        'fieldDescriptors': obj.element.aggregateRoot.fieldDescriptors,
                        'boundedContext': obj.element.boundedContext,
                    }


                    me.value.aggregate = tmpAgg

                } else if (obj.state == 'deleteRelation') {

                    var targetEventId = obj.element.sourceElement.elementView.id
                    me.value.relationEventInfo.some(function (eventItem, idx) {
                        if (eventItem.eventValue.elementView.id == targetEventId) {
                            me.value.relationEventInfo[idx] = null
                            return
                        }
                    })
                    me.value.relationEventInfo = me.value.relationEventInfo.filter(n => n)

                } else if (obj.state == 'addRelation') {

                    // var checked = false;
                    // me.value.relationEventInfo.forEach(function (eventItem, evtIdx) {
                    //
                    //     var evObj = {
                    //         relationId: obj.element.relationView.id,
                    //         eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement))
                    //     }
                    //
                    //     if (eventItem.relationId == obj.element.relationView.id) {
                    //         me.value.relationEventInfo[evtIdx] = evObj;
                    //         checked = true;
                    //     }
                    // })
                    //
                    // if (!checked) {
                    //     var evObj = {
                    //         relationId: obj.element.relationView.id,
                    //         eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement))
                    //     }
                    //     me.value.relationEventInfo.push(evObj)
                    // }


                }

                me.$nextTick(function () {
                    var designer = me.getComponent('modeling-designer')
                    designer.editing = true;
                })
            })
        },
        methods: {
            setNameCase:
                function (newVal) {
                    var me = this
                    me.value.name = newVal
                    me.value.namePascalCase = changeCase.pascalCase(newVal)
                    me.value.nameCamelCase = changeCase.camelCase(newVal)
                    me.value.namePlural = changeCase.camelCase(pluralize(newVal));
                },
            deleteCommand() {
                var me = this
                return new Promise(function (resolve) {
                    me.$EventBus.$off(`${me.value.elementView.id}`);

                    var obj = {
                        state: "deleteCommand",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.aggregate.id != null) {
                        me.$EventBus.$emit(me.value.aggregate.id, obj)
                    }
                    if (me.value.boundedContext.id != null) {
                        me.$EventBus.$emit(me.value.boundedContext.id, obj)
                    }


                    if (me.value.relationEventInfo.length > 0) {
                        me.value.relationEventInfo.forEach(function (CoItem) {
                            if (CoItem.relationId != null) {
                                //relation
                                me.$EventBus.$emit(`${CoItem.relationId}`, obj)
                                if (Object.keys(CoItem.eventValue).length > 0) {
                                    //event
                                    me.$EventBus.$emit(`${CoItem.eventValue.elementView.id}`, obj)
                                }

                            }

                        })
                    }

                    me.$emit('update:value', null);
                    resolve()
                })
            }

        }
    }
</script>


