<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                connectable
                :deletable=!isRead
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:rotateShape="onRotateShape"
                v-on:labelChanged="onLabelChanged"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >

            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#ED73B6',
          fill: '#ED73B6',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< External >>'">
                </text-element>
                <image-element
                        v-if="(Object.keys(value.aggregate).length === 0 && JSON.stringify(value.aggregate) === JSON.stringify({})) || value.name.length < 1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'External'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/external.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->
        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'External'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/external.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    export default {
        mixins: [Element],
        name: 'external-definition',
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.External'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    namePlural: '',

                    aggregate: {},
                    boundedContext: {},
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({}),
                        'angle': 0,
                    },
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    // drawer: false,
                    // selected: false,
                    editing: false,
                }
            }
        },
        data: function () {
            return {
                isRead:true,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if(this.namePanel !=null ){
                    this.namePanel =me.value.name
                }

                me.isRead = designer.isRead
            })
        },
        watch: {
            "value.boundedContext": function () {
                this.checkValue = true
            },
            // "value.name": function () {
            //     this.value.checkValue = true
            // },
            "openPanel":function (newVal) {
                var me = this
                if(newVal == false){

                    me.$nextTick(function () {
                        var obj = {
                            state: "updateExternal",
                            element: JSON.parse(JSON.stringify(me.value))
                        }

                        me.value.checkValue = false
                    })
                }

            },
            "value.name":function(newVal,oldVal){
                this.namePanel = newVal
            },
            // "value.drawer": _.debounce(
            //     function (newVal, oldVal) {
            //         var me = this
            //         if (newVal == false) {
            //             var obj = {
            //                 state: "updateExternal",
            //                 element: JSON.parse(JSON.stringify(me.value))
            //             }
            //
            //             me.value.checkValue = false
            //         }
            //
            //     }, 200)
        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                // console.log("eventValue", obj)

                if( obj.state== "delete" && obj.element._type == "org.uengine.modeling.model.Aggregate"){
                    me.value.aggregate={}
                }

                me.$nextTick(function () {
                    var designer = me.getComponent('modeling-designer')
                    designer.editing = true;
                })
            })

        },
        methods: {
            deleteExternal() {
                var me = this
                var designer = me.getComponent('modeling-designer')

                return new Promise(function (resolve) {
                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    var obj = {
                        state: "deleteExternal",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.aggregate.id != null) {
                        me.$EventBus.$emit(me.value.aggregate.id, obj)
                    }

                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


