<template>

    <v-container>
        <div>
            <v-divider></v-divider>
        </div>
        <v-row>
            <v-col
                    v-for="(item, key) in filteredVersionList"
                    :key="key"
                    cols="6"
                    md="4"
            >
                <v-card
                        outlined
                        class="mx-auto"
                        max-width="400"
                        max-height="400"
                >
                    <v-img
                            :src="item.versionImg"
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">Ver.{{ item.versionNum }}
                                <div style="font-size:15px; float: right">{{item.versionEmail}}</div>
                            </v-list-item-title>
                            <v-list-item-subtitle>Modified Date : {{ item.versionDate }}</v-list-item-subtitle>
                            <v-list-item-subtitle v-if=" `${item.versionType}` !='local' ">Comment : {{
                                item.versionComment }}
                            </v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <!-- toUrl(projectName, author, verName, type) -->
                        <v-btn text
                               @click="versionModalShow(item.versionValue)">
                            View
                        </v-btn>

                    </v-card-actions>
                </v-card>

            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>

        <v-layout>
            <modal name="view-modeling-designer-modal" :height='"80%"' :width="'80%'">
                <view-modeling-designer :versionValue="versionValue"></view-modeling-designer>
            </modal>
        </v-layout>


    </v-container>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "ViewEventStormingVersionPage",
        props: {},
        data() {
            return {
                versionList: [],
                name: '',
                search: '',
                loginAuthor: '',
                onLogin: false,
                //삭제 변수
                deleteDialog: false,
                checkAllVersion: false,
                versionValue: {},
                parmUid: '',
                parmType: '',
                parmProjectId: '',
                parmVersion: '',

            }
        },
        created() {
            var me = this;

            me.parmUid = this.$route.params.userUid;
            me.parmType = this.$route.params.projectType;
            me.parmProjectId = this.$route.params.projectId;
            me.parmVersion = this.$route.params.projectVersion;

            me.getVersionList()
            me.$nextTick(function () {
                me.filteredVersionList
            })
            // }
        },
        computed: {
            filteredVersionList() {
                var me = this


                if (me.search) {
                    return me.versionList.filter(eventstorming => {
                        return eventstorming.name.toLowerCase().includes(me.search.toLowerCase())
                    })
                } else {
                    return me.versionList
                }
            }
        },
        mounted: function () {
            var me = this

            // me.$EventBus.$on('login', function (newVal) {
            //     // console.log("----------------------")
            //     me.getVersionList()
            // })

        },
        methods: {
            versionModalShow(value) {
                this.versionValue = value
                this.$modal.show('view-modeling-designer-modal')
            },
            getVersionList() {
                var me = this
                var initValue = {
                    'definition': [],
                    'relation': []
                }
                var versionNum = 1;
                var list = []
                me.versionList = []

                if (me.parmType == 'local') {
                    var ob = {
                        versionNum: 1,
                        versionName: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'Local',
                        versionDate: localStorage.getItem("localSaveTime") != null ? localStorage.getItem("localSaveTime") : 'undefined',
                        versionComment: '',
                        versionEmail: localStorage.getItem("email") != null ? localStorage.getItem("email") : "anonymous",
                        versionImg: 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                        versionType: 'local',
                        versionValue: localStorage.getItem('localLoadData') != null ? localStorage.getItem('localLoadData') : initValue
                    }
                    list.push(ob)
                } else {
                    firebase.database()
                        .ref('/definitions/' + me.parmProjectId + '/versionLists')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                var versionNames = Object.keys(snapshot.val())
                                versionNames.forEach(function (version) {
                                    var versionDefinition = snapshot.val()[version]

                                    var obj = {
                                        versionNum: versionNum,
                                        versionComment: versionDefinition.versionInfo.comment,
                                        versionDate: versionDefinition.versionInfo.date,
                                        versionImg: versionDefinition.versionInfo.img,
                                        versionName: versionDefinition.versionInfo.projectName,
                                        versionEmail: versionDefinition.versionInfo.saveUserEmail,
                                        saveUserName: versionDefinition.versionInfo.saveUserName,
                                        versionValue: versionDefinition.versionValue.value
                                    }
                                    versionNum = versionNum + 1
                                    list.push(obj)

                                })
                            }
                        })
                }
                me.versionList = list

            },
            toUrl(author, projectType, projectId, projectVersion) {
                return "storming/" + author + "/" + projectType + "/" + projectId + "/" + projectVersion
            },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user.uid);
                        } else {
                            resolve("everyone")
                        }
                    });
                })
            },
        }
    }
</script>

