                me.selectDelete = JSON.parse(JSON.stringify(item))
                me.deleteDialog = true
            },
            deleteItem() {
                var me = this;
                var deleteItem, deletePermission;


                if (me.deleteConfirmCheckBox) {
                    if (me.selectDelete.type == true) {
                        deleteItem = firebase.database().ref('/saver/shared/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                    } else if (me.selectDelete.type == false) {
                        deleteItem = firebase.database().ref('/saver/mine/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                        deleteItem = firebase.database().ref('/saver/versionList/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                    } else if (me.selectDelete.type == 'Write' || me.selectDelete.type == 'ReadOnly') {
                        deleteItem = firebase.database().ref('/saver/multi/' + me.selectDelete.author + "/" + me.selectDelete.projectId)
                    }
                    if (me.selectDelete.projectType == 'mine') {
                        deleteItem = firebase.database().ref('/userLists/' + localStorage.getItem('uid') + '/' + me.selectDelete.projectType + "/" + me.selectDelete.projectId)
                    } else if (me.selectDelete.projectType == 'share') {
                        deleteItem = firebase.database().ref('/userLists/' + localStorage.getItem('uid') + '/' + me.selectDelete.projectType + '/' + me.selectDelete.projectId)
                        deletePermission = firebase.database().ref('/definitions/' + me.selectDelete.projectId + '/shareOptions/permissions/' + localStorage.getItem('uid'))
                    }


                    deleteItem.remove()
                    if (me.selectDelete.projectType == 'share') {
                        deletePermission.remove()
                    }
                    me.deleteConfirmCheckBox = false

                    me.$nextTick(function () {
                        me.getList()
                        me.deleteDialog = false
                    })

                } else {
                    alert("삭제를 원하시면 '☐' 체크 해주세요.")
                }

            },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user.uid);
                        } else {
                            resolve("everyone")
                        }
                    });
                })
            },
            getLocalList() {
                var me = this
                var filter

                return new Promise(function (resolve) {
                    var userId = localStorage.getItem("uid")

                    var local = [
                        {
                            author: localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous",
                            email: localStorage.getItem("email") != null ? localStorage.getItem("email") : "anonymous@sample.org",
                            date: localStorage.getItem("localSaveTime") != null ? localStorage.getItem("localSaveTime") : "undefined",
                            img: localStorage.getItem('localSaveImg') != null ? localStorage.getItem('localSaveImg') : 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                            userUid: userId != null ? userId : "anonymous",
                            projectId: 'init',
                            projectVersion: '-init',
                            projectName: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                            projectType: 'local',
                        },
                    ]
                    if(JSON.parse(localStorage.getItem('localLoadData')) != undefined){
                        if(JSON.parse(localStorage.getItem('localLoadData')).definition.length > 0){
                            me.stormingLocalList = local
                        }else{
                            me.stormingLocalList = []

                        }
                    }else{
                        me.stormingLocalList = []
                    }

                    resolve()
                })

            },
            getMineList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter = []
                me.stormingMineValueList = []

                return new Promise(function (resolve) {

                    firebase.database()
                        .ref('/userLists/' + userId + '/mine/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {

                            }
                        }).then(function (projectIds) {

                        if (projectIds.exists())
                            Object.keys(projectIds.val()).forEach(function (projectId, index) {
                                firebase.database()
                                    .ref('definitions/' + projectId + '/versionLists')
                                    .limitToLast(1)
                                    .once('value', function (snapshot) {

                                    }).then(function (get) {
                                    firebase.database()
                                        .ref('definitions/' + projectId + '/shareOptions')
                                        .once('value', function (snapshot) {
                                            if (snapshot.exists()) {

                                                var projectAuthor = snapshot.val()['author']
                                                var projectAuthorEmail = snapshot.val()['authorEmail']
                                                var projectShareCount = 0
                                                var lastVersion = Object.keys(get.val())[Object.keys(get.val()).length - 1]
                                                var lastVersionInfo = get.val()[lastVersion]['versionInfo']

                                                if (snapshot.val()['permissions'] != undefined) {
                                                    if (Object.keys(snapshot.val()['permissions']) == 'everyone') {
                                                        projectShareCount = -2
                                                    } else {
                                                        projectShareCount = Object.keys(snapshot.val()['permissions']).length
                                                    }
                                                } else {
                                                    projectShareCount = 0
                                                }

                                                var obj = {
                                                    projectId: projectId,
                                                    projectAuthor: projectAuthor,
                                                    projectAuthorEmail: projectAuthorEmail,
                                                    saveUser: lastVersionInfo.saveUser,
                                                    saveUserName: lastVersionInfo.saveUserName,
                                                    projectType: 'mine',
                                                    projectVersion: lastVersion,
                                                    comment: lastVersionInfo.comment,
                                                    date: lastVersionInfo.date,
                                                    saveUserEmail: lastVersionInfo.saveUserEmail,
                                                    img: lastVersionInfo.img,
                                                    projectName: lastVersionInfo.projectName,
                                                    write: true,
                                                    projectShareCount: projectShareCount,
                                                }

                                                if (filter.findIndex(x => x.projectId == obj.projectId) == -1) {
                                                    filter.push(obj)
                                                }

                                            }
                                        })
                                })


                            })

                        me.stormingMineValueList = filter
                    });
                    resolve()
                })
            },
            getShareList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter = []
                me.stormingShareValueList = []

                return new Promise(function (resolve) {

                    firebase.database()
                        .ref('/userLists/' + userId + '/share/')
                        .once('value', function (snapshot) {

                        }).then(function (projectIds) {
                        if (projectIds.exists())
                            Object.keys(projectIds.val()).forEach(function (projectId, index) {
                                firebase.database()
                                    .ref('definitions/' + projectId + '/versionLists')
                                    .limitToLast(1)
                                    .once('value', function (snapshot) {

                                    }).then(function (get) {
                                    firebase.database()
                                        .ref('definitions/' + projectId + '/shareOptions')
                                        .once('value', function (snapshot) {
                                            if (snapshot.exists()) {

                                                var projectAuthor = snapshot.val()['author']
                                                var projectAuthorEmail = snapshot.val()['authorEmail']
                                                var projectShareCount = 0
                                                var lastVersion = Object.keys(get.val())[Object.keys(get.val()).length - 1]
                                                var lastVersionInfo = get.val()[lastVersion]['versionInfo']

                                                var permissionWrite = false
                                                if (projectAuthor == userId) {
                                                    permissionWrite = true
                                                } else {
                                                    if (snapshot.val()['permissions'] != undefined) {
                                                        permissionWrite = snapshot.val()['permissions'][userId].write
                                                    }
                                                }

                                                var obj = {
                                                    projectId: projectId,
                                                    projectAuthor: projectAuthor,
                                                    projectAuthorEmail: projectAuthorEmail,
                                                    saveUser: lastVersionInfo.saveUser,
                                                    saveUserName: lastVersionInfo.saveUserName,
                                                    projectType: 'share',
                                                    projectVersion: lastVersion,
                                                    comment: lastVersionInfo.comment,
                                                    date: lastVersionInfo.date,
                                                    saveUserEmail: lastVersionInfo.saveUserEmail,
                                                    img: lastVersionInfo.img,
                                                    projectName: lastVersionInfo.projectName,
                                                    write: permissionWrite,
                                                    projectShareCount: projectShareCount,
                                                }

                                                if (filter.findIndex(x => x.projectId == obj.projectId) == -1) {
                                                    filter.push(obj)
                                                }
                                            }
                                        })
                                })

                            })
                        me.stormingShareValueList = filter
                    });
                    resolve()
                })
            },

            getEveryOneList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter = []
                me.stormingEveryValueList = []

                return new Promise(function (resolve) {
                    firebase.database()
                        .ref('/userLists/everyone/share/')
                        .once('value', function (snapshot) {

                        }).then(function (projectIds) {

                        if (projectIds.exists())
                            Object.keys(projectIds.val()).forEach(function (projectId, index) {
                                firebase.database()
                                    .ref('definitions/' + projectId + '/versionLists')
                                    .limitToLast(1)
                                    .once('value', function (snapshot) {

                                    }).then(function (get) {

                                    firebase.database()
                                        .ref('definitions/' + projectId + '/shareOptions')
                                        .once('value', function (snapshot) {
                                            if (snapshot.exists()) {

                                                var projectAuthor = snapshot.val()['author']
                                                var projectAuthorEmail = snapshot.val()['authorEmail']
                                                var projectShareCount = 0
                                                var lastVersion = Object.keys(get.val())[Object.keys(get.val()).length - 1]
                                                var lastVersionInfo = get.val()[lastVersion]['versionInfo']

                                                var permissionWrite = false
                                                // if (projectAuthor == userId) {
                                                //     permissionWrite = true
                                                // }


                                                var obj = {
                                                    projectId: projectId,
                                                    projectAuthor: projectAuthor,
                                                    projectAuthorEmail: projectAuthorEmail,
                                                    saveUser: lastVersionInfo.saveUser,
                                                    saveUserName: lastVersionInfo.saveUserName,
                                                    projectType: 'every',
                                                    projectVersion: lastVersion,
                                                    comment: lastVersionInfo.comment,
                                                    date: lastVersionInfo.date,
                                                    saveUserEmail: lastVersionInfo.saveUserEmail,
                                                    img: lastVersionInfo.img,
                                                    projectName: lastVersionInfo.projectName,
                                                    write: permissionWrite,
                                                    projectShareCount: projectShareCount,
                                                }

                                                if ((filter.findIndex(x => x.projectId == obj.projectId) == -1) && obj.projectAuthor != localStorage.getItem("uid")) {
                                                    filter.push(obj)

                                                }
                                            }
                                        })

                                })
                            })
                        me.stormingEveryValueList = filter
                        resolve()
                    });
                })
            },
            getList() {
                var me = this

                return new Promise(function (resolve) {

                    var userId = localStorage.getItem("uid")

                    if (window.localStorage.getItem("accessToken") != null) {
                        if (userId) {

                            me.getLocalList().then(function () {
                                me.getMineList().then(function () {
                                    me.getShareList().then(function () {
                                        me.getEveryOneList().then(function () {
                                            console.log('Get LSIT 1 ')
                                            resolve()
                                        })
                                    })
                                })
                            })

                            // me.getLocalList().then(function () {
                            //     resolve()
                            // })
                            // me.getMineList().then(function () {
                            //     resolve()
                            // })
                            // me.getShareList().then(function () {
                            //     resolve()
                            // })
                            // me.getEveryOneList().then(function () {
                            //     resolve()
                            // })


                        } else {
                            me.getLocalList().then(function () {
                                me.getEveryOneList().then(function () {
                                    me.$EventBus.$emit('progressValue', false)
                                    resolve()
                                })
                            })

                            // me.getLocalList().then(function () {
                            //     resolve()
                            // })
                            // me.getEveryOneList().then(function () {
                            //     resolve()
                            // })

                        }
                    } else {
                        me.getLocalList().then(function () {
                            me.getEveryOneList().then(function () {
                                me.$EventBus.$emit('progressValue', false)
                                resolve()
                            })
                        })

                        // me.getLocalList().then(function () {
                        //     resolve()
                        // })
                        // me.getEveryOneList().then(function () {
                        //     resolve()
                        // })

                    }

                })


            }
        }
    }
</script>

