<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div class="canvas-panel">
        <!-- Code Modal -->
        <v-layout right>
            <opengraph ref="opengraph" focus-canvas-on-select wheelScalable :labelEditable="false"
                       :dragPageMovable="dragPageMovable" :enableContextmenu="false" :enableRootContextmenu="false"
                       :enableHotkeyCtrlC="false" :enableHotkeyCtrlV="false"
                       :enableHotkeyDelete="false" :enableHotkeyCtrlZ="false" :enableHotkeyCtrlD="false"
                       :enableHotkeyCtrlG="false" :slider="true" :movable="false" :resizable="true" :selectable="true"
                       :deleteable="false"
                       :connectable="false" v-if="value" v-on:canvasReady="bindEvents" :autoSliderUpdate="true"
                       :imageBase="imageBase">
                <!--엘리먼트-->
                <div v-for="(element, index) in value.definition">
                    <component :is="getComponentByClassName(element._type)"
                               :value.sync="value.definition[index]"></component>
                </div>
                <div v-for="(element, index) in value.relation">
                    <component :is="getComponentByClassName(element._type)"
                               :value.sync="value.relation[index]"></component>
                </div>
            </opengraph>

            <v-layout right>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>

                <v-row style="margin-top: 10px; margin-right: 5px; width: 30%" justify="end" align="start">
                    <v-text-field
                            :width="300"
                            disabled
                            label="Project Name" v-model="projectName"
                            @click="unselectedAll">
                    </v-text-field>
                </v-row>
            </v-layout>


            <v-card class="tools" style="top:100px; text-align: center;">
                <span class="bpmn-icon-hand-tool" v-bind:class="{ icons : !dragPageMovable, hands : dragPageMovable }"
                      _width="30"
                      _height="30" v-on:click="toggleGrip">
                     <v-tooltip md-direction="right">Hands</v-tooltip>
                </span>
            </v-card>

        </v-layout>

        <modeler-image-generator ref="modeler-image-generator"></modeler-image-generator>

    </div>
</template>

<script>
    import TextReader from "@/components/yaml.vue";
    import CodeViewer from "../CodeViewer";
    import {
        saveAs
    } from 'file-saver';
    // import firebase from 'firebase'
    import {
        mdiAbTesting
    } from '@mdi/js';

    var _ = require('lodash')

    var pluralize = require('pluralize');
    var path = require('path')


    export default {
        name: 'view-modeling-designer',
        components: {
            TextReader,
            saveAs,
            CodeViewer,
            mdiAbTesting,
        },
        props: {
            versionValue: Object,
        },
        data() {
            return {
                isShare: false,
                onlineSaveDialog: false,
                isLoadVersion: false,
                //server
                author: '',
                serverComment: '',
                serverDate: '',
                editing: true,
                canvas: null,
                dragPageMovable: false,
                value: {
                    'definition': [],
                    'relation': []
                },
                projectName: '',
                generateZipDialog: false,
                //undo Redo
                undoRedoStorage: 5,
                undoRedoArray: [],
                undoRedoIndex: 0,
                undoRedoLastIndex: 0,
                imageBase: 'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/symbol/',
                //data structure
                model: [],
                fullPathList: [],
                treeList: [],
                getCodeList: [],
                openCode: [],
                template: '',
                treeOpen: true,
                gitUrl: '',
                gitPath: [],
                gitCode: [],
                checkName: true,
            }
        },
        beforeDestroy: function () {
            // console.log(this.canvas)
            this.canvas.removeSlider();
        },
        computed: {
            id: {
                get: function () {
                    return this.projectName
                }
            },
        },
        created: function () {
            var me = this

            this.$nextTick(function(){
                if(me.versionValue){
                    me.value =JSON.parse(me.versionValue)
                }
            })



        },
        mounted() {
            var me = this


            this.$nextTick(function () {

                let startTime = new Date().getTime()

                this.canvas._CONFIG.FAST_LOADING = false;

                this.$refs.opengraph.printTimer(startTime, new Date().getTime());


                if (this.$route.params.projectName && this.$route.params.author) {
                    me.projectName = this.$route.params.projectName;

                    if (window.localStorage.getItem("author") == null) {
                        me.author = this.$route.params.author
                    } else {
                        me.author = window.localStorage.getItem("author")
                    }

                }

            });

        },
        watch: { },
        methods: {
            loadParser: function (loadJson) {
                var me = this
                if (!loadJson.relation) {
                    loadJson.relation = []
                }
                if (!loadJson.definition) {
                    loadJson.definition = []
                }

                me.value = loadJson;

                me.value.relation.forEach(function (relationItem, reIdx) {
                    me.value.definition.forEach(function (sourceItem, sourceIdx) {
                        if (sourceItem.elementView.id == relationItem.sourceElement.elementView.id) {
                            relationItem.sourceElement = sourceItem
                        }
                        if (sourceItem.elementView.id == relationItem.targetElement.elementView.id) {
                            relationItem.targetElement = sourceItem
                        }
                    })
                })
                me.value.definition.forEach(function (bdItem, bdIdx) {
                    if (bdItem._type == 'org.uengine.modeling.model.BoundedContext') {
                        bdItem.aggregates.forEach(function (aggItem, agIdx) {
                            me.value.definition.forEach(function (originAggItem) {
                                if (aggItem.elementView.id == originAggItem.elementView.id) {
                                    bdItem.aggregates[agIdx] = originAggItem
                                }
                            })
                        })
                    }
                })
                me.value.definition.forEach(function (aggItem, agIdx) {
                    if (aggItem._type == "org.uengine.modeling.model.Aggregate") {

                        if (aggItem.events.length > 0) {
                            aggItem.events.forEach(function (eventItem, evIndex) {
                                me.value.definition.some(function (originEventItem) {

                                    if (eventItem.elementView.id == originEventItem.elementView.id) {
                                        aggItem.events[evIndex] = originEventItem;
                                    }
                                })
                            })
                        }

                        if (aggItem.commands.length > 0) {
                            aggItem.commands.forEach(function (commandItem, coIdx) {
                                me.value.definition.some(function (originCommandItem) {

                                    if (commandItem.elementView.id == originCommandItem.elementView.id) {
                                        aggItem.commands[coIdx] = originCommandItem;
                                    }
                                })
                            })
                        }
                        if (aggItem.policies.length > 0) {
                            aggItem.policies.forEach(function (policyItem, poIdx) {
                                me.value.definition.some(function (originPolicyItem) {

                                    if (policyItem.elementView.id == originPolicyItem.elementView.id) {
                                        aggItem.policies[poIdx] = originPolicyItem;
                                    }

                                })
                            })
                        }
                    }
                })
            },
            unselectedAll: function (newVal) {
                // console.log(newVal)
                this.value.definition.forEach(function (definition) {
                    definition.selected = false
                })
                this.value.relation.forEach(function (relation) {
                    relation.selected = false
                })
            },
            ajax: function (url, method, payload, successCallback) {
                var xhr = new XMLHttpRequest();
                xhr.open(method, url, true);
                // xhr.withCredentials = true;
                xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
                xhr.onreadystatechange = function () {
                    if (xhr.readyState != 4 || xhr.status != 200) return;
                    successCallback(xhr.responseText);
                };
                xhr.send(JSON.stringify(payload));
            },
            restApiPush: function () {
                var me = this;
                me.$http.post(`http://localhost:8080/event/${me.projectName}`, me.value, {
                        responseType: "arraybuffer",
                        headers: {
                            'Content-Type': 'application/zip;'
                        }
                    }
                ).then(function (response) {
                    var blob = new Blob([response.data], {type: 'application/zip'});
                    var fileName = me.projectName + ".zip";
                    saveAs(blob, fileName);
                })
            },
            b64toBlob: function (b64Data, contentType, sliceSize) {
                contentType = contentType || '';
                sliceSize = sliceSize || 512;

                var byteCharacters = atob(b64Data);
                var byteArrays = [];

                for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
                    var slice = byteCharacters.slice(offset, offset + sliceSize);

                    var byteNumbers = new Array(slice.length);
                    for (var i = 0; i < slice.length; i++) {
                        byteNumbers[i] = slice.charCodeAt(i);
                    }

                    var byteArray = new Uint8Array(byteNumbers);

                    byteArrays.push(byteArray);
                }

                var blob = new Blob(byteArrays, {type: contentType});
                return blob;
            },
            toggleGrip: function () {
                this.dragPageMovable = !this.dragPageMovable;

                if (this.dragPageMovable) {
                    this.cursorStyle = 'cursor: url("/static/image/symbol/hands.png"), auto;';
                    this.handsStyle = ' color: #ffc124;';
                } else {
                    this.cursorStyle = null;
                    this.handsStyle = null;
                }
            },
            bindEvents: function (opengraph) {
                var me = this;
                var el = me.$el;
                var canvasEl = $(opengraph.container);
                if (!canvasEl || !canvasEl.length) {
                    return;
                }
                this.canvas = opengraph.canvas;
                //아이콘 드래그 드랍 이벤트 등록
                $(el).find('.draggable').draggable({
                    start: function () {
                        canvasEl.data('DRAG_SHAPE', {
                            'component': $(this).attr('_component'),
                            'width': $(this).attr('_width'),
                            'height': $(this).attr('_height')
                        });
                    },
                    helper: 'clone',
                    appendTo: canvasEl
                });

                canvasEl.droppable({
                    drop: function (event, ui) {
                        var componentInfo = canvasEl.data('DRAG_SHAPE'),
                            shape, element;
                        if (componentInfo) {
                            var dropX = event.pageX - canvasEl.offset().left + canvasEl[0].scrollLeft;
                            var dropY = event.pageY - canvasEl.offset().top + canvasEl[0].scrollTop;

                            dropX = dropX / opengraph.scale;
                            dropY = dropY / opengraph.scale;

                            componentInfo = {
                                component: componentInfo.component,
                                x: dropX,
                                y: dropY,
                                width: parseInt(componentInfo.width, 10),
                                height: parseInt(componentInfo.height, 10),
                                label: ''
                            }
                            me.addElement(componentInfo);
                        }
                        canvasEl.removeData('DRAG_SHAPE');
                    }
                });
            },
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            },
            getComponentByName: function (name) {
                var componentByName;
                $.each(window.Vue._components, function (i, component) {
                    if (component.name == name) {
                        componentByName = component;
                    }
                });
                return componentByName;
            },
            getComponentByClassName: function (className) {
                var componentByClassName;

                $.each(window.Vue.classModelingComponents, function (i, component) {
                    if (component.default.computed && component.default.computed.className && component.default.computed.className() == className) {
                        componentByClassName = component.default;
                    }
                });
                return componentByClassName;
            },
            // recursiveDeepCopy(o) {
            //     var me = this
            //     var newO,
            //         i;
            //
            //     if (typeof o !== 'object') {
            //         return o;
            //     }
            //     if (!o) {
            //         return o;
            //     }
            //
            //     if ('[object Array]' === Object.prototype.toString.apply(o)) {
            //         newO = [];
            //         for (i = 0; i < o.length; i += 1) {
            //             newO[i] = me.recursiveDeepCopy(o[i]);
            //         }
            //         return newO;
            //     }
            //
            //     newO = {};
            //     for (i in o) {
            //         if (o.hasOwnProperty(i)) {
            //             newO[i] = me.recursiveDeepCopy(o[i]);
            //         }
            //     }
            //     return newO;
            // }
        }
    }
</script>

