            var me = this

            //database db setting
            me.database = firebase.database()
            window.io = io

            me.$EventBus.$emit('progressValue', true)

            me.parmUid = this.$route.params.userUid;
            me.parmType = this.$route.params.projectType;
            me.parmProjectId = this.$route.params.projectId;
            me.parmVersion = this.$route.params.projectVersion;


            me.roomId = 'msaez_' + me.parmProjectId


            var valueInit = {"definition": [], "relation": []}

            var userUid = localStorage.getItem('uid')


            if (me.parmType == 'local') {
                localStorage.setItem('projectId', me.parmProjectId)
                me.projectName = localStorage.getItem('localSaveProjectName') != null ? localStorage.getItem('localSaveProjectName') : 'local'

                me.isRead = false
                if (localStorage.getItem('localLoadData') != null) {
                    me.value = JSON.parse(localStorage.getItem('localLoadData'));
                    me.$nextTick(function () {
                        me.$EventBus.$emit('progressValue', false)
                    })

                } else {
                    me.value = valueInit
                    me.$nextTick(function () {
                        me.$EventBus.$emit('progressValue', false)
                    })


                }
            }

            else if (me.parmType == 'mine' || me.parmType == 'share' || me.parmType == 'every') {
                localStorage.setItem('projectId', me.parmProjectId)
                me.isLoad = true

                me.database.ref('definitions/' + me.parmProjectId)
                    .child('/versionLists/' + me.parmVersion)
                    .once('value', function (snapshot) {
                        if (snapshot.exists()) {

                            console.log('>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>Created<<<<<<<<<<<<<<<<<<<<<<<<<<<<')
                            me.value = JSON.parse(snapshot.val()['versionValue'].value)
                            me.projectName = snapshot.val()['versionInfo'].projectName

                            if (me.parmType == 'mine' || me.parmType == 'every') {
                                if (snapshot.val()['versionValue'].queue == undefined) {
                                    me.$nextTick(function () {
                                        me.$EventBus.$emit('progressValue', false)
                                    })
                                }
                            }
                        }
                    }).then(function (snapshot) {

                    if (me.parmType == 'share') {
                        me.database.ref('definitions/' + me.parmProjectId)
                            .child('/shareOptions/permissions/' + localStorage.getItem('uid'))
                            .once('value', function (snapshot) {
                                if (snapshot.exists()) {

                                    if (snapshot.val().write == true) {
                                        me.isRead = false
                                    } else {
                                        me.isRead = true
                                        me.forkDialog = true
                                    }
                                }
                            })
                        me.$nextTick(function () {
                            me.$EventBus.$emit('progressValue', false)
                        })
                    } else if (me.parmType == 'mine') {

                        me.database.ref('definitions/' + me.parmProjectId)
                            .child('/shareOptions')
                            .once('value', function (snapshot) {
                                if (snapshot.exists()) {

                                    me.projectAuthor = snapshot.val()['author']
                                    if(me.projectAuthor == localStorage.getItem('uid') ){
                                        me.isRead = false
                                    }else{
                                        me.isRead = true
                                    }

                                    if (snapshot.val().permissions != undefined) {
                                        if(Object.keys(snapshot.val().permissions).indexOf('everyone') != -1){
                                            me.sharedCount = -2
                                        }else{
                                            me.sharedCount = Object.keys(snapshot.val().permissions).length
                                        }
                                    } else {
                                        me.sharedCount = 0
                                    }
                                }
                            })

                        me.$nextTick(function () {
                            me.$EventBus.$emit('progressValue', false)
                        })
                    } else if (me.parmType == 'every') {
                        me.isRead = true
                        me.$nextTick(function () {
                            me.$EventBus.$emit('progressValue', false)
                        })
                    }

                })
            }
        },
