                }


            })

            if (me.parmType != 'local') {

                if (me.parmType == 'share' || me.parmType == 'mine') {
                    var getDiffRef = me.database.ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion + '/versionValue/queue')
