                                    })
                                }

                            }
                            me.$EventBus.$emit('progressValue', false)
                            // })
                        });
                } else if (me.parmType == 'every') {

                    me.database.ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion + '/versionValue/value')
                        .on('value', function (value) {
                            if (value.exists()) {
                                if (me.isMyEdit == true) {
                                    me.isMyEdit = false
                                } else {
                                    me.isOtherEdit = true
                                    console.log('>>>>>>>>>>>>> Value Mounted<<<<<<<<<<<<<<<<<<<')
                                    me.value = JSON.parse(value.val())
                                }
                            }
                            me.$EventBus.$emit('progressValue', false)
                        });
                }


                if (me.parmType == 'share') {
                    me.database.ref('definitions/' + me.parmProjectId)
                        .child('/shareOptions/permissions/' + localStorage.getItem('uid'))
                        .on('child_changed', function (child_changed) {
                            if (child_changed.exists()) {
                                console.log(child_changed.val())
                                if (me.parmType == 'mine') {
                                    me.isRead = false
                                } else {
                                    if (child_changed.val() == true) {
                                        me.isRead = false
                                    } else {
                                        me.isRead = true
                                        me.forkDialog = true
                                    }
                                }

                            }
                        })

                    me.database.ref('definitions/' + me.parmProjectId)
                        .child('/shareOptions/permissions/' + localStorage.getItem('uid'))
                        .on('child_removed', function (child_removed) {
                            if (child_removed.exists()) {
                                if (me.parmType == 'mine') {
                                    me.isRead = false
                                } else {
                                    me.isRead = true
                                    me.$router.push('/')
                                    alert('Permission denied')
                                }
                            }
                        })
                } else if (me.parmType == 'mine') {
                    me.database.ref('definitions/' + me.parmProjectId)
                        .child('/shareOptions')
                        .on('value', function (value) {
                            if (value.exists()) {
                                if (value.val().permissions != undefined) {
                                    me.sharedCount = Object.keys(value.val().permissions).length
                                } else {
                                    me.sharedCount = 0
                                }
                            }
                        })
                } else if (me.parmType == 'every') {
                    if (localStorage.getItem('uid') == me.parmUid) {
                        me.isRead = false
                    } else {
                        me.isRead = true
                    }

                }
            }


            if (me.$route.params.type) {
                me.$EventBus.$on('chatKey', function (val) {
                    // console.log(val)
                    me.chatUid = val;
                    me.messageRef = firebase.database().ref(`/${me.$route.params.author}/${me.$route.params.projectName}`);
                })
            }

            //undoRedo()
            me.$EventBus.$on('undoRedo', function (json) {

                if (me.undoRedoIndex == me.undoRedoLastIndex) {
                    if (me.undoRedoArray.length == 0) {
                        // me.undoRedoArray.push(JSON.stringify(me.undoRedoInitValue))
                        me.undoRedoArray.push(JSON.stringify(json))
                        me.undoRedoIndex = me.undoRedoIndex + 1
                        me.undoRedoLastIndex = me.undoRedoIndex
                    } else if (me.undoRedoArray.length < me.undoRedoStorage) {
                        me.undoRedoArray.push(JSON.stringify(json))
                        me.undoRedoIndex = me.undoRedoIndex + 1
                        me.undoRedoLastIndex = me.undoRedoIndex
                    } else {
                        me.undoRedoArray.splice(0, 1)
                        me.undoRedoArray.push(JSON.stringify(json))
                    }
                } else {
                    console.log("중간에 PUSH")
                    me.undoRedoArray.splice(me.undoRedoIndex, me.undoRedoArray.length - me.undoRedoIndex)
                    if (me.undoRedoArray.length < me.undoRedoStorage) {
                        me.undoRedoIndex = me.undoRedoIndex + 1
                        me.undoRedoLastIndex = me.undoRedoIndex
                        me.undoRedoArray.push(JSON.stringify(json))
                    } else {
                        me.undoRedoArray.splice(0, 1)
                        me.undoRedoArray.push(JSON.stringify(json))
                    }
                }


                me.$nextTick(function () {
                    me.editing = false;
                })
            });

            // DeployData CallBack
            me.$EventBus.$on('deploy', function (boundedId) {
                me.getBoundedSetting(boundedId).then(function (resolve) {
                    me.generateZipToFireBase(resolve, boundedId)
                })
            });

            //중간저장
            me.$EventBus.$on('storage', function (newVal) {
                var saveDate = JSON.parse(JSON.stringify(me.value))
                saveDate.definition = saveDate.definition.filter(n => n)
                saveDate.relation = saveDate.relation.filter(n => n)

                if (me.parmType == 'local') {
                    //로컬 저장
                    localStorage.removeItem('localLoadData')
                    localStorage.removeItem('localSaveTime')
                    localStorage.removeItem('localSaveProjectName')

                    //local save
                    localStorage.setItem('localLoadData', JSON.stringify(saveDate))
                    localStorage.setItem('localSaveTime', me.getNowTime())
                    localStorage.setItem('localSaveProjectName', me.projectName)

                } else if (me.parmType == 'mine') {

                    if (me.sharedCount == 0) {
                        var valueRef = me.database.ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion)

                        valueRef
                            .once('value', function (snapshot) {
                                if (snapshot.exists()) {
                                    if (snapshot.val()['versionValue'].queue != undefined) {
                                        valueRef.child('/versionValue/queue').remove()
                                    }
                                }
                            }).then(function () {
                            valueRef.child('/versionValue').update({
                                value: JSON.stringify(saveDate)
                            })

                        })
                    }

                }

            });

            this.$nextTick(function () {
                // me.messageRef.on('child_added', function (value) {
                //     // console.log(value.val())
                // });

                let startTime = new Date().getTime()

                this.canvas._CONFIG.FAST_LOADING = false;


                this.$refs.opengraph.printTimer(startTime, new Date().getTime());

                $(document).keydown((evt) => {
                    // var drawerCheck = false;
                    // me.value.definition.forEach(function (item) {
                    //     console.log(item)
                    //     if (item != null) {
                    //         // if (item.drawer) {
                    //         //     //Drawer가 있으면 true
                    //         //     drawerCheck = true;
                    //         //
                    //         // }
                    //     }
                    // })
                    // if (drawerCheck) {
                    //     return;
                    // }
                    if (evt.keyCode == 67 && (evt.metaKey || evt.ctrlKey)) {
                        me.copy();
                    } else if (evt.keyCode == 86 && (evt.ctrlKey || evt.metaKey)) {
                        me.paste();

                    } else if (evt.keyCode == 90 && (evt.metaKey || evt.ctrlKey)) {
                        if (evt.shiftKey) {
                            me.redo();
                        } else {
                            me.undo();

                        }
                    }

                });

                // if (this.$route.params.projectName && this.$route.params.author) {
                //     me.projectName = this.$route.params.projectName;
                //
