                //     // // console.log(result.data)
                //     me.loadAddElement(result.data);
                // })
                // }

            });

        },
        watch: {
            webrtcDialog: function (newVal, oldVal) {
                if (newVal == false) {
                    this.onLeave()
                }
            },
            checkPreviewValueRelation: {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    var delta

                    delta = jsondiffpatch.diff(oldVal, newVal);
                    console.log(delta)
                    if (delta != undefined) {
                        me.database
                            .ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion + '/versionValue/queue')
                            .push({
                                editUid: localStorage.getItem('uid'),
                                timeStamp: new Date().getTime(),
                                diff: JSON.stringify(delta)
                            })
                    }
                }

            },
            checkValueRelation: {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    var delta


                    if (me.isLoad == false) {
                        if (me.isUndoRedo == false) {
                            delta = jsondiffpatch.diff(oldVal, newVal);
                            if (delta != undefined) {
                                console.log("UndoRedo>>", delta)

                                if (me.parmType != 'local') {

                                    if ((me.sharedCount == 0||me.sharedCount == -2) && me.parmType == 'mine') {
                                        me.$EventBus.$emit('storage')
                                    } else {
                                        if (me.isOtherEdit == true) {
                                            me.isOtherEdit = false
                                        } else {
                                            me.isMyEdit = true
                                            me.database
                                                .ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion + '/versionValue/queue')
                                                .push({
                                                    editUid: localStorage.getItem('uid'),
                                                    timeStamp: new Date().getTime(),
                                                    diff: JSON.stringify(delta)
                                                })
                                        }

                                    }


                                    me.$EventBus.$emit('undoRedo', delta)

                                } else {
                                    me.$EventBus.$emit('undoRedo', delta)
                                    me.$EventBus.$emit('storage')
                                }
                            }
                        }
                        me.isUndoRedo = false
                    } else {
                        me.isLoad = false
                    }

                }
            },
            "projectName":
                _.debounce(
                    function (newVal) {
                        this.$EventBus.$emit('storage')
                    }, 200
                ),
            messageRef:

                function (newVal) {
                    // var me = this
                    // newVal.on('child_added', function (value) {
                    //     // console.log(value.val())
                    //     if (value.val().uid != localStorage.getItem('uid')) {
                    //         var element = JSON.parse(value.val().value)
                    //         me.editing = false;
                    //         me.$EventBus.$emit(`${element.elementView.id}`, element)
                    //     }
                    // });
                },
            fullPathList: function (newVal) {
                var me = this
                me.treeList = newVal.reduce((children, value) => {
                    value.fullPath.split('/').reduce((folder, o, i) => {

                        if (i === 0) {
                            var p = folder;
                        } else {
                            var p = folder.children = folder.children || [];
                        }

                        let f = p.find(x => x.name === o.trim());

                        if (!f) {

                            f = {
                                name: o.trim(),
                                key: me.findFile(value.key, o.trim()).key,
                                file: me.findFile(value.key, o.trim()).file,
                                code: me.findFile(value.key, o.trim()).code,
                            }

                            p.push(f);
                        }

                        return f;
                    }, children);

                    return children;
                }, []);
            },
            async template() {
                var me = this
                if (me.template == 'GIT') {
                    await me.gitTemplate().then(function () {
                        me.getListSetting()

                        me.$nextTick(function () {
                            me.treeOpen = true
                        })
                    })
                } else {
                    me.getListSetting()

                    me.$nextTick(function () {
                        me.treeOpen = true
                    })
                }

            }
        },
        methods: {
            duplicateCheckProjectName() {
                var me = this

                me.duplicateprojectName = false
            },
            removeParticipant(person) {
                var me = this

                if (me.parmUid == localStorage.getItem('uid')) {

                    if(person.userName == 'Everyone'){
                        console.log('everyone')
                        me.participantsLists.splice(me.participantsLists.findIndex(x => x.userName == person.userName), 1)
                        me.database
                            .ref('definitions/' + me.parmProjectId + '/shareOptions/permissions/everyone')
                            .remove()

                        me.shareEveryOne = false
                    }else{
                        me.participantsLists.splice(me.participantsLists.findIndex(x => x.uid == person.uid), 1)

                        me.database
                            .ref('userLists/' + person.uid + '/share/' + me.parmProjectId + '/projectId')
                            .remove()
                            .then(function () {
                                me.database
                                    .ref('definitions/' + me.parmProjectId + '/shareOptions/permissions/' + person.uid)
                                    .remove()
                            })
                    }


                } else {
                    alert("Can't remove Participant (No permission)")
                }
            },
            syncDifff(diff) {
                var me = this
                return new Promise(function (resolve) {
                    jsondiffpatch.patch(me.value, diff)
                    resolve()
                })
            },
            shareDialogShowList() {
                var me = this

                me.participantsLists = []

                me.database
                    .ref('definitions/' + me.parmProjectId + '/shareOptions/permissions')
                    .once('value', function (snapshot) {
                        if (snapshot.exists()) {
                            console.log(snapshot.val())
                            if(Object.keys(snapshot.val()) == 'everyone'){

                                me.shareEveryOne  =true
                                var obj={
                                    userPic:'',
                                    userName: 'Everyone',
                                    write:false,

                                }
                                if (me.participantsLists.findIndex(x => x.userName == obj.userName) == -1) {
                                    me.participantsLists.push(obj)
                                }
                            }else{
                                Object.keys(snapshot.val()).forEach(function (uid) {
                                    var obj = {
                                        uid: uid,
                                        userName: snapshot.val()[uid].userName,
                                        userPic: snapshot.val()[uid].userPic,
                                        email: snapshot.val()[uid].email,
                                        write: snapshot.val()[uid].write
                                    }
                                    if (me.participantsLists.findIndex(x => x.uid == obj.uid) == -1) {
                                        me.participantsLists.push(obj)
                                    }
                                })
                            }

                        }
                    })

                me.shareDialog = true
            },
            addParticipants() {
                var me = this
                var findUserUid = ''
                var findUserName = ''
                var findUserPicture = ''


                if (me.participantsEmail != '') {
                    var w = false
                    if (me.isMultiShareType == 'ReadOnly') {
                        w = false
                    } else if (me.isMultiShareType == 'Write') {
                        w = true
                    }


                    me.database
                        .ref('users/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                var userlist = snapshot.val()
                                Object.keys(userlist).some(function (uid) {
                                    if (userlist[uid].email == me.participantsEmail) {
                                        findUserUid = uid
                                        findUserName = userlist[uid].username
