                                            // delete,create
                                            if (changeValue[0] == null) {
                                                console.log('Muti-Delete')
                                                var el = changeValue[1]
                                                var obj = {
                                                    state: 'delete',
                                                    element: el
                                                }
                                                if (Object.keys(changeValue[1]).length > 0) {
                                                    me.$EventBus.$emit(`${el.elementView.id}`, obj)
                                                }

                                            } else {
                                                console.log('Muti-CREATE')
                                                me.makeElement(changeValue[0], definitionIndex)

                                            }
                                        } else {
                                            console.log('Muti-Edit')
                                            // jsondiffpatch.patch(me.value.definition[definitionIndex], changeValue);
                                            jsondiffpatch.patch(me.value.definition[definitionIndex], jsondiffpatch.reverse(changeValue))
                                        }

                                    })
                                }
