                var me = this
                if (title == 'Save to Server') {
                    me.uploadServerDialog()
                } else if (title == 'Download model File') {
                    me.download()
                } else if (title == 'Open from Files') {
                    //서버 파일 오픈
                    me.goToServer()
                } else if (title == 'Open from Local') {
                    me.loadTextFromFile()
                } else if (title == 'Code Preview') {
                    me.codeModalShow()
                } else if (title == 'Download Archive') {
                    me.generateZipDialog = true
                } else if (title == 'Share') {
                    me.shareDialogShowList()
                }
