            modelSetting() {
                // USE server Save, downLoad
                var me = this

                me.fullPathList = []
                me.getCodeList = []
                me.treeList = []

                me.model = {
                    _type: "org.uengine.modeling.model.Model",
                    boundedContexts: [],
                    relations: [],
                    projectName: me.projectName
                }

                return new Promise(function (resolve) {
                    var copyValue = JSON.parse(JSON.stringify(me.value))

                    //boundedContexts 넣기
                    copyValue.definition.forEach(function (item) {
                        if (item != null) {
                            if (item._type == 'org.uengine.modeling.model.BoundedContext') {
                                me.model.boundedContexts.push(item)
                            }
                        }
                    })
                    var modelForElements = {
                        BoundedContext: [],
                        AggregateRoot: [],
                        Aggregate: [],
                        Policy: [],
                        Event: [],
                        Command: [],
                        View: [],
                        RelationEventInfo: [],
                        RelationCommandInfo: [],
                        ViewEventInfo: [],
                    };

                    //선 정보
                    me.model.relations = me.recursiveDeepCopy(me.value.relation)

                    //element 정
                    me.model.boundedContexts.forEach(function (bound, idx) {
                        bound.portGenerated = 8081 + idx
                        modelForElements["BoundedContext"].push(bound);
                        bound.groups.forEach(function (groupsId) {
                            if (me.$refs[groupsId][0].value._type == 'org.uengine.modeling.model.Event') {
                                if (me.$refs[groupsId][0].value.aggregate.name == undefined) {
                                    var tempEvent = me.$refs[groupsId][0].value
                                    modelForElements['Event'].push(tempEvent)
                                }

                            }
                        })
                        me.model.boundedContexts[idx].aggregates.forEach(function (agg, idx) {
                            // fieldDescriptors 전처리
                            agg.aggregateRoot.fieldDescriptors.forEach(function (fd) {
                                // fd.nameCamelCase = _.camelCase(fd.name)
                                // fd.namePascalCase = fd.nameCamelCase.substring(0, 1).toUpperCase() + fd.nameCamelCase.substring(1);
                                if (fd.isKey)
                                    agg.aggregateRoot.keyFieldDescriptor = fd;
                            })


                            modelForElements["Aggregate"].push(agg);
                            modelForElements["AggregateRoot"].push(agg.aggregateRoot);

                            if (agg.commands)
                                agg.commands.forEach(function (co) {
                                    if (co.name == '') {
                                        me.text = "Check Command Name"
                                        me.snackbar = true
                                        me.color = "error"
                                        me.timeout = 1000
                                        me.checkName = false
                                    }
                                    modelForElements["Command"].push(co);
                                })

                            if (agg.events)
                                agg.events.forEach(function (ev) {
                                    if (ev.name == '') {
                                        me.text = "Check Event Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.color = "error"
                                        me.checkName = false
                                    }
                                    modelForElements["Event"].push(ev);
                                })

                        })

                        me.model.boundedContexts[idx].policies.forEach(function (po) {
                            if (po.name == '') {
                                me.text = "Check Element Name"
                                me.snackbar = true
                                me.timeout = 1000
                                me.color = "error"

                                me.checkName = false
                            }
                            // console.log(po)
                            po.relationEventInfo.forEach(function (item) {
                                // console.log(item)
                                item.boundedContext = {}
                                item.boundedContext.name = po.boundedContext.name
                                item.elementView = {}
                                item.elementView.id = po.elementView.id
                                // item.eventValue.boundedContext.name = po.boundedContext.name
                                modelForElements["RelationEventInfo"].push(item)
                            })
                            modelForElements["Policy"].push(po);
                        })

                        me.model.boundedContexts[idx].viewes.forEach(function (vi) {
                            if (vi.name == '') {
                                me.text = "Check Element Name"
                                me.snackbar = true
                                me.timeout = 1000
                                me.color = "error"

                                me.checkName = false
                            }
                            // console.log(po)

                            vi.createRules.forEach(function (fd, index) {

                                if (Object.keys(fd.when).length == 0) {
                                    vi.createRules[index] = null
                                    vi.createRules = vi.createRules.filter(n => n)
                                } else {
                                    if (fd.when.boundedContext.id != vi.boundedContext.id) {
                                        var temp = JSON.parse(JSON.stringify(fd))
                                        temp.when.boundedContext = vi.boundedContext
                                        modelForElements["ViewEventInfo"].push(temp.when)
                                    }
                                    fd.fieldMapping.forEach(function (item, idx) {
                                        if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                            fd.fieldMapping[idx] = null
                                            fd.fieldMapping = fd.fieldMapping.filter(n => n)
                                        }
                                    })
                                    fd._index = index + 1
                                }

                            })

                            vi.updateRules.forEach(function (fd, index) {
                                if (Object.keys(fd.when).length == 0) {
                                    vi.updateRules[index] = null
                                    vi.updateRules = vi.updateRules.filter(n => n)
                                } else {
                                    if (fd.when.boundedContext.id != vi.boundedContext.id) {
                                        var temp = JSON.parse(JSON.stringify(fd))
                                        temp.when.boundedContext = vi.boundedContext
                                        modelForElements["ViewEventInfo"].push(temp.when)
                                    }
                                    fd.fieldMapping.forEach(function (item, idx) {
                                        if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                            fd.fieldMapping[idx] = null
                                            fd.fieldMapping = fd.fieldMapping.filter(n => n)
                                        }
                                    })
                                    fd.where.forEach(function (item, idx) {
                                        if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                            fd.where[idx] = null
                                            fd.where = fd.where.filter(n => n)
                                        }
                                    })
                                    fd._index = index + 1
                                }

                            })
                            vi.deleteRules.forEach(function (fd, index) {
                                if (Object.keys(fd.when).length == 0) {
                                    vi.deleteRules[index] = null
                                    vi.deleteRules = vi.deleteRules.filter(n => n)
                                } else {
                                    if (fd.when.boundedContext.id != vi.boundedContext.id) {
                                        var temp = JSON.parse(JSON.stringify(fd))
                                        temp.when.boundedContext = vi.boundedContext
                                        modelForElements["ViewEventInfo"].push(temp.when)
                                    }
                                    fd.where.forEach(function (item, idx) {
                                        if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                            fd.where[idx] = null
                                            fd.where = fd.where.filter(n => n)
                                        }
                                    })
                                    fd._index = index + 1
                                }
                            })

                            modelForElements["View"].push(vi);
                        })
                    });
                    resolve()
                })

