            bindEvents: function (opengraph) {
                var me = this;
                var el = me.$el;
                var canvasEl = $(opengraph.container);
                if (!canvasEl || !canvasEl.length) {
                    return;
                }
                this.canvas = opengraph.canvas;
                //아이콘 드래그 드랍 이벤트 등록
                $(el).find('.draggable').draggable({
                    start: function () {
                        canvasEl.data('DRAG_SHAPE', {
                            'component': $(this).attr('_component'),
                            'width': $(this).attr('_width'),
                            'height': $(this).attr('_height')
                        });
                    },
                    helper: 'clone',
                    appendTo: canvasEl
                });

                canvasEl.droppable({
                    drop: function (event, ui) {
                        var componentInfo = canvasEl.data('DRAG_SHAPE'),
                            shape, element;
                        if (componentInfo) {
                            var dropX = event.pageX - canvasEl.offset().left + canvasEl[0].scrollLeft;
                            var dropY = event.pageY - canvasEl.offset().top + canvasEl[0].scrollTop;
                            var scale = opengraph.canvas._CONFIG.SLIDER[0].innerText / 100

                            // dropX = dropX / opengraph.scale;
                            // dropY = dropY / opengraph.scale;


                            dropX = dropX / scale;
                            dropY = dropY / scale;

                            componentInfo = {
                                component: componentInfo.component,
                                x: dropX,
                                y: dropY,
                                width: parseInt(componentInfo.width, 10),
                                height: parseInt(componentInfo.height, 10),
                                label: ''
                            }

                            me.addElement(componentInfo);
                            // me.addElementServer(componentInfo)
                        }
                        canvasEl.removeData('DRAG_SHAPE');
                    }
                });
            },
            dbuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + s4() + s4() + s4() + s4() + s4() + s4();
            },
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            },
            onConnectShape: function (edge, from, to) {

                var me = this;
                //존재하는 릴레이션인 경우 (뷰 컴포넌트), 데이터 매핑에 의해 자동으로 from, to 가 변경되어있기 때문에 따로 로직은 필요없음.
                //=> 바뀌어야 함.
                //신규 릴레이션인 경우에는 릴레이션 생성
                var edgeElement, originalData;
                var isComponent = false;
                if (edge.shape) {
                    edgeElement = edge;
                } else {
                    isComponent = true;
                    edgeElement = edge.element;
                }
                // console.log(from, to)

                if (edgeElement && from && to) {
                    var vertices = '[' + edgeElement.shape.geom.vertices.toString() + ']';
                    var componentInfo = {
                        component: 'class-relation',
                        sourceElement: from.$parent,
                        targetElement: to.$parent,
                        vertices: vertices,
                        isFilled: true,
                        relationView: {
                            style: JSON.stringify({}),
                            value: vertices,
                        }
                    }

                    from.$parent.value.elementView.id = from.id;
                    to.$parent.value.elementView.id = to.id;

                    if (isComponent) {
                        me.canvas.removeShape(edgeElement, true);
                        //this.removeComponentByOpenGraphComponentId(edgeElement.id);
                        //기존 컴포넌트가 있는 경우 originalData 와 함께 생성
                        this.addElement(componentInfo)
                    } else {
                        me.canvas.removeShape(edgeElement, true);
                        //기존 컴포넌트가 없는 경우 신규 생성
                        this.addElement(componentInfo);
                    }
                    // this.syncOthers();
                }
            }
            ,

            makeElement(ele, idx) {
                var me = this;
                var component
                var check = true

                if (ele._type == "org.uengine.modeling.model.Event") {
                    component = me.getComponentByName('domain-event-definition');
                } else if (ele._type == "org.uengine.modeling.model.BoundedContext") {
                    component = me.getComponentByName('bounded-context-definition');
                } else if (ele._type == "org.uengine.modeling.model.Aggregate") {
                    component = me.getComponentByName('aggregate-definition');
                } else if (ele._type == "org.uengine.modeling.model.Command") {
                    component = me.getComponentByName('command-definition');
                } else if (ele._type == "org.uengine.modeling.model.Policy") {
                    component = me.getComponentByName('policy-definition');
                } else if (ele._type == "org.uengine.modeling.model.View") {
                    component = me.getComponentByName('view-definition');
                } else if (ele._type == "org.uengine.modeling.model.External") {
                    component = me.getComponentByName('external-definition');
                } else if (ele._type == "org.uengine.modeling.model.Relation") {
                    component = me.getComponentByName('class-relation');
                }

                if (ele._type == "org.uengine.modeling.model.Relation") {
                    check = false
                } else {
                    check = true
                }


                if (check) {
                    var element = component.computed.createNew(
                        ele.elementView.id,
                        ele.elementView.x,
                        ele.elementView.y,
                        ele.elementView.width,
                        ele.elementView.height
                    )

                    // element.name = "tmp";
                    // element.loadData = true;
                    if (component.name == 'bounded-context-definition') {
                        element.aggregates = ele.aggregates
                        element.policies = ele.policies
                    } else if (component.name == 'aggregate-definition') {
                        element.aggregateRoot = ele.aggregateRoot
                        element.boundedContext = ele.boundedContext
                        if (ele.events) {
                            element.events = ele.events
                        } else {
                            element.events = []
                        }
                        if (ele.commands) {
                            element.commands = ele.commands
                        } else {
                            element.commands = []
                        }
                        if (ele.viewes) {
                            element.viewes = ele.viewes
                        } else {
                            element.viewes = []
                        }
                    } else if (component.name == 'domain-event-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = pluralize(ele.aggregate.name);
                        }
                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        // element.eventToPolicy = ele.eventToPolicy

                        element.relationPolicyInfo = ele.relationPolicyInfo
                        element.relationCommandInfo = ele.relationCommandInfo

                        element.fieldDescriptors = ele.fieldDescriptors
                        element.trigger = ele.trigger
                    } else if (component.name == 'command-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        }

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.restfulType = ele.restfulType
                    } else if (component.name == 'policy-definition') {
                        // if (ele.aggregate) {
                        //     ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                        //     ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                        //     ele.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        // }

                        // element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
