                                            var ele = me.makeElement(ev)
                                            agg.events[idx] = ele
                                        }
                                    })
                                }
                                if (agg.commands) {
                                    agg.commands.forEach(function (co, idx) {
                                        if (agg.commands[idx].elementView.id == co.elementView.id) {
                                            var ele = me.makeElement(co)
                                            agg.commands[idx] = ele
                                        }
                                    })
                                }

                                if (agg.viewes) {
                                    agg.viewes.forEach(function (vi, idx) {
                                        if (agg.viewes[idx].elementView.id == vi.elementView.id) {
                                            var ele = me.makeElement(vi)
                                            agg.viewes[idx] = ele
                                        }
                                    })
                                }
                            })
                            if (bounded.policies) {
                                bounded.policies.forEach(function (po, idx) {
                                    if (bounded.policies[idx].elementView.id == po.elementView.id) {
                                        var ele = me.makeElement(po)
                                        bounded.policies[idx] = ele
                                    }

                                })
                            }
                        }
                    })

                if (value.relations)
                    value.relations.forEach(function (line) {
                        me.makeElement(line)
                    })

            },
