                                    if (ele._type == 'org.uengine.modeling.model.Model') {
                                        // /src/main/java/com/example/Application.java
                                        // gateway2/src/main/java/com/example/template/Application.java

                                        codeValue = {
                                            'key': elementPath[1] + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': elementPath[1] + fileName,
                                            'fullPath': fullPath.replace('for-model/', '')
                                        }
                                    } else {
                                        codeValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fullPath': fullPath
                                        }
                                    }


                                    getCodeList.push(codeValue)
                                    fullPathList.push(fullValue)
                                }
                            }


                            if (Array.isArray(model)) {
                                model.forEach(process);
                            } else {
                                process(model);
                            }

                        } //end of doit


                        var xhttp = new XMLHttpRequest();
                        xhttp.onreadystatechange = function () {

                            if (this.readyState == 4 && this.status == 200) {
                                if (me.template == 'GIT') {
                                    var gitCodeObj = JSON.parse(this.responseText);
                                    var gitCodeTmp = Base64.decode(gitCodeObj.content)
                                    doit(gitCodeTmp)
                                } else {
                                    doit(this.responseText);
                                }
                                if (lastIndex == index) {
                                    var tmp = {
                                        'getCodeList': getCodeList,
                                        'fullPathList': fullPathList
                                    }
                                    resolve(tmp);
                                }
                            }
                        };
                        if (me.template == 'GIT') {
                            xhttp.open("GET", me.gitCodeUrl[element], true);
                            xhttp.send();
                        } else {
                            xhttp.open("GET", "/static/templates/" + template + "/" + element, true);
                            xhttp.send();
                        }


                    });

                })

            },
        }
    }
</script>

