<template>
    <div>
        <group-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                :deletable=!isRead
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :label.sync="namePanel"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addToGroup="onAddToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :_style="{stroke:'black',
                'vertical-align': 'top',
                                'font-weight': 'bold',
                                'font-size': '16',

                        }">

            <storming-sub-controller :type="type"></storming-sub-controller>
            <sub-elements>
                <!--title-->
                <!--                <text-element-->
                <!--                        :sub-width="'100%'"-->
                <!--                        :sub-height="titleH"-->
                <!--                        :sub-top="0"-->
                <!--                        :sub-left="0"-->
                <!--                        :sub-style="{'font-weight': 'bold','font-size': '16'}"-->
                <!--                        :title.sync="value.name"-->
                <!--                        :text.sync="value.name">-->
                <!--                </text-element>-->

            </sub-elements>
        </group-element>

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                titleName="BoundedContext"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'
    import GroupElement from "../../../opengraph/shape/GroupElement";
    import ImageElement from "../../../opengraph/shape/ImageElement";

    var changeCase = require('change-case');
    var pluralize = require('pluralize');


    export default {
        components: {ImageElement, GroupElement},
        mixins: [Element],
        name: 'bounded-context-definition',
        props: {},
        computed: {

            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.BoundedContext'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: 'BoundedContext' + x,
                    namePlural: '',
                    aggregates: [],
                    policies: [],
                    viewes: [],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 300,
                        'height': 300,
                        'style': JSON.stringify({})
                    },
                    // drawer: false,
                    // checkValue: false,
                    // selected: false,
                    portGenerated: 0,
                    groups: [],
                    tempId: ''
                }
            }
        },
        data: function () {
            return {
                isRead:false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if(this.namePanel !=null ){
                    this.namePanel =me.value.name
                }

                me.isRead = designer.isRead
            })
        },
        watch: {
            // "value.selected": function (newVal) {
            //     var me = this
            //     // if(newVal){
            //     //     this.value.groups.forEach(function (ele) {
            //     //         var obj={
            //     //            state: 'updateBounded',
            //     //             element: me.value
            //     //         }
            //     //         me.$EventBus.$emit(`${ele}`,obj)
            //     //     })
            //     // }
            // },
            // 'value.name': function (newVal, oldVal) {
            //     var me = this
            //     me.value.checkValue = true
            // },
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
                this.checkValue = true
            },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {


                    me.$nextTick(function () {
                        me.value.name = me.namePanel

                        var obj = {
                            state: 'updateBounded',
                            element: JSON.parse(JSON.stringify(me.value))
                        }

                        me.value.groups.forEach(function (ele) {
                            me.$EventBus.$emit(ele, obj)
                        })
                        // me.value.aggregates.forEach(function (agg) {
                        //     me.$EventBus.$emit(agg.elementView.id, obj)
                        // })
                        // me.value.policies.forEach(function (agg) {
                        //     me.$EventBus.$emit(agg.elementView.id, obj)
                        // })
                        // me.value.viewes.forEach(function (agg) {
                        //     me.$EventBus.$emit(agg.elementView.id, obj)
                        // })
                        me.checkValue = false

                    })

                }

            },
            // "value.drawer": function (newVal) {
            //     var me = this
            //     if (newVal == false) {
            //         if (me.value.checkValue) {
            //             if (me.value.aggregates) {
            //                 var obj = {
            //                     state: 'updateBounded',
            //                     element: JSON.parse(JSON.stringify(me.value))
            //                 }
            //
            //                 me.value.groups.forEach(function (ele) {
            //                     me.$EventBus.$emit(`${ele}`, obj)
            //                 })
            //                 // me.value.aggregates.forEach(function (agg) {
            //                 //     me.$EventBus.$emit(agg.elementView.id, obj)
            //                 // })
            //                 // me.value.policies.forEach(function (agg) {
            //                 //     me.$EventBus.$emit(agg.elementView.id, obj)
            //                 // })
            //                 // me.value.viewes.forEach(function (agg) {
            //                 //     me.$EventBus.$emit(agg.elementView.id, obj)
            //                 // })
            //             }
            //             me.value.checkValue = false
            //         }
            //     }
            // }

        },
        mounted: function () {
            var me = this
            var boundedId = this.value.elementView.id

            var $tr = $('#' + boundedId);
            $tr.parent().children().first().before($tr)

            var me = this
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if (obj.state == 'deleteAggregate') {
                    me.value.aggregates.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.aggregates[idx] = null
                            me.value.aggregates = me.value.aggregates.filter(n => n);
                            return;
                        }
                    })

                    me.value.groups.some(function (item, idx) {
                        if (item == obj.element.elementView.id) {
                            me.value.groups[idx] = null
                            me.value.groups = me.value.groups.filter(n => n);
                            return;
                        }
                    })
                } else if (obj.state == 'updateAggregate') {
                    me.value.aggregates.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.aggregates[idx] = obj.element
                        }
                    })
                } else if (obj.state == 'updatePolicy') {
                    me.value.policies.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.policies[idx] = obj.element
                        }
                    })
                } else if (obj.state == 'deletePolicy') {
                    me.value.policies.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.policies[idx] = null
                            me.value.policies = me.value.policies.filter(n => n);
                        }
                    })
                    me.value.groups.some(function (item, idx) {
                        if (item == obj.element.elementView.id) {
                            me.value.groups[idx] = null
                            me.value.groups = me.value.groups.filter(n => n);
                            return;
                        }
                    })
                } else if (obj.state == 'deleteEvent') {
                    me.value.groups.some(function (item, idx) {
                        if (item == obj.element.elementView.id) {
                            me.value.groups[idx] = null
                            me.value.groups = me.value.groups.filter(n => n);
                            return;
                        }
                    })
                } else if (obj.state == 'deleteView') {
                    me.value.groups.some(function (item, idx) {
                        if (item == obj.element.elementView.id) {
                            me.value.groups[idx] = null
                            me.value.groups = me.value.groups.filter(n => n);
                            return;
                        }
                    })
                } else if (obj.state == 'deleteCommand') {
                    me.value.groups.some(function (item, idx) {
                        if (item == obj.element.elementView.id) {
                            me.value.groups[idx] = null
                            me.value.groups = me.value.groups.filter(n => n);
                            return;
                        }
                    })
                }
                if (obj.state == 'updateView') {
                    me.value.viewes.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.viewes[idx] = obj.element
                        }
                    })
                }
                if (obj.state == 'deleteView') {
                    me.value.viewes.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.viewes[idx] = null
                            me.value.viewes = me.value.viewes.filter(n => n);
                        }
                    })
                }

            })
        },
        methods: {
            deleteBoundedContext() {
                var me = this
                return new Promise(function (resolve) {
                    me.value.aggregates.forEach(function (aggregate) {
                        console.log(aggregate.elementView.id)
                        var obj = {
                            state: "deleteBounded",
                            element: me.value
                        }
                        me.$EventBus.$emit(aggregate.elementView.id, obj)
                    })
                    me.deleteInBounded(me.value.name)
                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    // me.$EventBus.$emit('storage')
                    resolve()
                })
            },
            deleteInBounded(boundedName) {
                var me = this
                var designer = this.getComponent('modeling-designer')

                designer.value.definition.forEach(function (item, idx) {
                    if (item != null) {
                        if (item.boundedContext && item.boundedContext.name == boundedName) {
                            // console.log("boundedItem Id : ", item.elementView.id)
                            me.$EventBus.$off(`${item.elementView.id}`);
                            designer.value.definition[idx] = null;
                        }
                    }
                })
                // me.$EventBus.$emit('storage')
            }
        }
    }
</script>


