<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                :deletable=!isRead
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:rotateShape="onRotateShape"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5FC08B',
          fill: '#5FC08B',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< View >>'">
                </text-element>
                <image-element
                        v-if="value.name.length < 1 "
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'View'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/view.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'View'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/view.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    export default {
        mixins: [Element],
        name: 'view-definition',
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.View'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    aggregate: {},
                    namePascalCase: '',
                    namePlural: '',
                    boundedContext: {},
                    fieldDescriptors: [
                        {
                            _type: "org.uengine.model.FieldDescriptor",
                            name: "id",
                            className: "Long",
                            nameCamelCase: 'id',
                            namePascalCase: 'Id',
                            isKey: true
                        }
                    ],
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({})
                    },
                    // checkValue: false,
                    // drawer: false,
                    // selected: false,
                    editing: false,
                    editingView: false,
                    dataProjection: 'cqrs',
                    createRules: [
                        {
                            _type: 'viewStoreRule',
                            operation: 'CREATE',
                            when: {},
                            fieldMapping: [{"viewField": {}, "eventField": {}}],
                            where: [{"viewField": {}, "eventField": {}}],
                        }
                    ],
                    updateRules: [
                        {
                            _type: 'viewStoreRule',
                            operation: 'UPDATE',
                            when: {},
                            fieldMapping: [{"viewField": {}, "eventField": {}}],
                            where: [{"viewField": {}, "eventField": {}}],
                        }
                    ],
                    deleteRules: [
                        {
                            _type: 'viewStoreRule',
                            operation: 'DELETE',
                            when: {},
                            fieldMapping: [{"viewField": {}, "eventField": {}}],
                            where: [{"viewField": {}, "eventField": {}}],
                        }
                    ],


                }
            }
        },
        data: function () {
            return {
                isRead:true,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {

            var me = this
            this.$nextTick(function () {
                if(this.namePanel !=null ){
                    this.namePanel =me.value.name
                }

                if (me.$route.params.userUid  == localStorage.getItem('uid') || me.$route.params.userUid  == 'anonymous') {
                    //myself
                    me.isRead = false
                } else if (localStorage.getItem('uid') == null) {
                    //logout
                    me.isRead = false
                } else {
                    //other
                    if (me.$route.params.type == 'ReadOnly') {
                        me.isRead = true
                    } else if (me.$route.params.type == 'Write') {
                        me.isRead = false
                    }
                }
            })

        },
        watch: {
            "value.name":function(newVal,oldVal){
                this.namePanel = newVal
                this.checkValue = true
            },
            "openPanel":function (newVal) {
                var me = this
                if(newVal == false){
                    me.$nextTick(function () {
                        me.setNameCase(me.namePanel)

                        var obj = {
                            state: "updateView",
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)

                        me.value.checkValue = false
                    })
                }

            },
            "value.dataProjection": function (newVal, oldVal) {
                var me = this
                // console.log(newVal)
                if (newVal == 'cqrs') {
                    me.value.checkValue = true
                } else if (newVal == 'uimashup') {
                } else if (newVal == 'graphql') {
                }
            }
        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {


                console.log(obj)
                if (obj.state == 'delete') {

                    if (obj.element._type == "org.uengine.modeling.model.Aggregate") {
                        me.value.aggregate = {}
                    }

                } else if (obj.state == 'updateAggregate') {
                    me.value.aggregate = {}

                    var tmpAgg = {
                        'id': obj.element.elementView.id,
                        'name': obj.element.name,
                        'namePlural': obj.element.namePlural,
                        'namePascalCase': obj.element.namePascalCase,
                        'nameCamelCase': obj.element.nameCamelCase,
                        'fieldDescriptors': obj.element.aggregateRoot.fieldDescriptors
                    }

                    me.value.aggregate = tmpAgg

                } else if (obj.state == 'updateBounded') {
                    console.log("View > updateBounded")
                    me.value.boundedContext.name = obj.element.name;
                    me.value.boundedContext.id = obj.element.elementView.id

                    // var sendObj = {
                    //     state: 'updateView',
                    //     element: me.value
                    // }
                    //
                    // me.$EventBus.$emit(obj.element.elementView.id, sendObj)
                }


                // if (obj.state == "delete" && obj.element._type == "org.uengine.modeling.model.Aggregate") {
                //     me.value.aggregate = {}
                // } else if (obj.element._type == me.value._type) {
                //     //aggregate
                //     if (me.value.aggregate) {
                //         me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                //     }
                //
                //     me.deleteActivity(obj)
                //
                // }

                me.$nextTick(function () {
                    var designer = me.getComponent('modeling-designer')
                    designer.editing = true;
                })
            })


            var tmp = me.value.dataProjection;
            // me.value.dataProjection = '';
            this.$nextTick(function () {
                if (tmp == 'cqrs') {
                    // // console.log("")
                    // console.log(me.value.dataProjection)

                    // me.value.viewHeaders = [
                    //     {
                    //         text: 'Key',
                    //         align: 'left',
                    //         sortable: false,
                    //         value: 'isKey',
                    //     },
                    //     {text: 'columnName', value: 'name'},
                    //     {text: 'type', value: 'className'},
                    //     {text: 'sourceEvent', value: 'sourceEvent'},
                    //     {text: 'sourceEvent or DirectValue', value: 'eventDirectValue'},
                    //     {text: 'viewColumnName', value: 'viewColumnName'},
                    //     {text: 'sourceEventColumn', value: 'sourceEventColumn'},
                    //     {text: 'Actions', value: 'action'},
                    // ]

                    // me.value.viewHeaders = [
                    //     {text: 'viewColumn', value: 'viewColumn'},
                    //     {text: 'viewColumnType', value: 'viewColumnType'},
                    //     {text: 'selectEvent', value: 'selectEvent'},
                    //     {text: 'sourceEventColumn', value: 'sourceEventColumn'},
                    //     {text: 'defaultValue', value: 'defaultValue'},
                    //     {text: 'Actions', value: 'action'}
                    // ]
                    // me.value.viewFieldDescriptors = me.value.fieldDescriptorsCqrs
                } else if (tmp == 'uimashup') {
                    // console.log(me.value.dataProjection)

                    // me.value.viewHeaders = [
                    //     {text: 'type', value: 'className'},
                    // ]
                    //
                    // me.value.viewFieldDescriptors = me.value.fieldDescriptorsMash
                } else if (tmp == 'graphql') {
                    // console.log(me.value.dataProjection)

                }
            })
        },
        methods: {
            cleanObject(obj) {
                for (var propName in obj) {
                    if (typeof obj[propName] == "object")
                        this.cleanObject(obj[propName])
                    if (obj[propName] === null || obj[propName] === undefined)
                        delete obj[propName];
                }
            },
            deleteView() {
                var me = this

                return new Promise(function (resolve) {
                    me.$EventBus.$off(`${me.value.elementView.id}`);

                    var obj = {
                        state: "deleteView",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.aggregate.id != null) {
                        me.$EventBus.$emit(me.value.aggregate.id, obj)
                    }
                    if (me.value.boundedContext.id != null) {
                        me.$EventBus.$emit(me.value.boundedContext.id, obj)
                    }
                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


