<template>
    <div>
        <edge-element
                v-if="value.sourceElement._type == 'org.uengine.modeling.model.Event' && value.targetElement._type != 'org.uengine.modeling.model.Event'"
                selectable
                connectable
                deletable
                :vertices.sync="vertices"
                :from.sync="value.from"
                :to.sync="value.to"
                :_style="style_"
                :label="value.name"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:removeShape="onRemoveShape(value)"
        >
        </edge-element>

        <!--        <uml-property-panel-->
        <!--            v-if="value.sourceElement._type == 'org.uengine.uml.model.Class'"-->
        <!--            v-model="value"-->
        <!--            :titleName="'uml-Relation'"-->
        <!--        ></uml-property-panel>-->

        <!--        <modeling-property-panel-->
        <!--                v-if="value.sourceElement._type != 'org.uengine.modeling.model.Policy'"-->
        <!--                v-model="value"-->
        <!--                :titleName="'Relation'"-->
        <!--        ></modeling-property-panel>-->
    </div>
</template>

<script>
    import Element from '../../modeling/Element'
    import UmlPropertyPanel from "../../modeling/UMLPropertyPanel";

    export default {
        components: {UmlPropertyPanel},
        mixins: [Element],
        name: 'class-relation',
        props: {
            value: Object
        },

        created: function () {
            var me = this
            if (this.value && this.value.relationView) {
                this.value.from = this.value.relationView.from;
                this.value.to = this.value.relationView.to;
            }

        },
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Relation'
            },
            style_() {
                var style = {}
                if (this.value.targetElement._type == 'org.uengine.modeling.model.Policy') {
                    style = {
                        "arrow-end": "block",
                        'stroke-width': '1.3',
                        'stroke-dasharray': '- ',
                        'font-size': 20,
                    }
                } else if (this.value.targetElement._type == 'org.uengine.modeling.model.Command') {
                    style = {
                        "arrow-end": "block",
                        'font-size': 20,
                    }
                }
                //     } else if (this.value._type == "org.uengine.modeling.model.Relation" &&
                //         this.value.relationValue == 'GET' &&
                //         this.value.relationValue == 'POST' &&
                //         this.value.relationValue == 'PATCH' &&
                //         this.value.relationValue == 'DELETE') {
                //         style = {
                //             "arrow-end": "block",
                //             'font-size': 20,
                //         }
                //     } else if (this.value.relationValue == '1') {
                //         style = {
                //             'stroke-width': '1.3',
                //             'font-size': 30,
                //             'marker': {
                //                 'start': {
                //                     'id': 'OG.marker.RectangleMarker',
                //                     'size': [20, 20]
                //                 },
                //                 'mid': {
                //                     'id': 'OG.marker.RectangleMarker',
                //                     'size': [20, 20]
                //                 },
                //                 'end': {
                //                     'id': 'OG.marker.RectangleMarker',
                //                     'size': [20, 20]
                //                 }
                //             }
                //         }
                //         this.value.name = "1:1"
                //     } else if (this.value.relationValue == '2') {
                //         style = {
                //             'arrow-start': 'block',
                //             "arrow-end": "diamond",
                //             'stroke-width': '1.3',
                //             'font-size': 30,
                //
                //         }
                //         this.value.name = "1:1"
                //
                //     } else if (this.value.relationValue == '3') {
                //         style = {
                //             'arrow-start': 'diamond',
                //             "arrow-end": "block",
                //             'stroke-width': '1.3',
                //             'font-size': 30,
                //         }
                //         this.value.name = "0:N"
                //
                //     }
                return style
            },
            createNew(elementId, from, to, vertices) {
                return {
                    _type: this.className(),
                    name: '',
                    // relationKey: '',
                    // relationValue: '',
                    sourceElement: from,
                    targetElement: to,
                    from: from.elementView.id,
                    to: to.elementView.id,
                    // checkValue: false,
                    // selected: false,
                    relationView: {
                        id: elementId,
                        style: JSON.stringify({
                            "arrow-start": "none",
                            "arrow-end": "none",
                        }),
                        value: vertices,
                        from: from.elementView.id,
                        to: to.elementView.id,
                        needReconnect: true,
                    },
                    sourceMultiplicity: 1,
                    targetMultiplicity: 1,
                    // drawer: false
                }
            },
            vertices: {
                get: function () {
                    var style;
                    try {
                        return JSON.parse(this.value.relationView.value);
                    } catch (e) {
                        return null;
                    }
                },
                set: function (val) {
                    this.value.relationView.value = JSON.stringify(val);
                }
            }
        },
        data: function () {
            return {}
        },
        watch: {

        },
        mounted: function () {
            var me = this
            this.$EventBus.$on(`${me.value.relationView.id}`, function (obj) {
                if (obj.state == 'deleteEvent') {
                    me.deleteRelation()
                }
                if (obj.state == 'deletePolicy') {
                    me.deleteRelation()
                }
                if (obj.state == 'deleteCommand') {
                    me.deleteRelation()
                }
                if (obj.state == 'deleteRelation') {
                    me.deleteRelation()
                }

            })

            var obj = {
                state: "addRelation",
                element: JSON.parse(JSON.stringify(me.value))
            }


            me.$EventBus.$emit(me.value.sourceElement.elementView.id, obj)
            me.$EventBus.$emit(me.value.targetElement.elementView.id, obj)


            // console.log(me.value)
            // var obj = {
            //     state: "addRelation",
            //     element: JSON.parse(JSON.stringify(me.value))
            // }
            // console.log('>>>>>>>>>>>>>>>',obj)
            // me.$EventBus.$emit(me.value.sourceElement.elementView.id, obj)
            // me.$EventBus.$emit(me.value.targetElement.elementView.id, obj)

        },
        methods: {
            deleteRelation() {
                var me = this

                return new Promise(function (resolve) {
                    // console.log("deleteRelation")
                    //evnet, policy 쪽 정보 업데이트
                    me.$EventBus.$off(`${me.value.relationView.id}`);

                    var obj = {
                        state: "deleteRelation",
                        element: me.value
                    }
                    if (me.value.from) {
                        me.$EventBus.$emit(me.value.from, obj)
                    }
                    if (me.value.to) {
                        me.$EventBus.$emit(me.value.to, obj)
                    }

                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


