<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                connectable
                :deletable=!isRead
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:rotateShape="onRotateShape"
                v-on:labelChanged="onLabelChanged"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F1A746',
          fill: '#F1A746',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements v-for="i in editUserImg.length">
                <image-element
                        v-bind:image="editUserImg[i-1]"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(i-1))+'px'"
                        :sub-bottom="'100px'"
                >
                </image-element>
            </sub-elements>

            <sub-elements>

                <image-element
                        v-if="(  Object.keys(value.aggregate).length === 0 ) || value.name.length < 1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Event >>'">
                </text-element>

            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                titleName="Event"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/event.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');

    var Mustache = require('mustache')
    import * as _ from 'lodash';

    export default {
        mixins: [Element],
        name: 'domain-event-definition',
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Event'
            },
            createNew(elementId, x, y, width, height, angle) {
                return {
                    _type: this.className(),

                    name: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    namePlural: '',

                    aggregate: {},
                    boundedContext: {},
                    fieldDescriptors: [
                        {
                            _type: "org.uengine.model.FieldDescriptor",
                            name: "id",
                            className: "Long",
                            nameCamelCase: 'id',
                            namePascalCase: 'Id',
                            isKey: true
                        }
                    ],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({}),
                        'angle': 0,
                    },
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    checkAlert: true,
                    // checkValue: false,
                    relationPolicyInfo: [],
                    relationCommandInfo: [],
                    trigger: '@PrePersist',

                    // isUndoRedo: false,
                    editing: false,

                }
            },

        },
        data: function () {
            return {
                isRead: false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }

                me.isRead = designer.isRead

            })

        },
        watch: {
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
            },
            "value.relationPolicyInfo": {
                deep: true,
                handler: function () {
                    this.checkValue = true
                }
            },
            "value.relationCommandInfo": {
                deep: true,
                handler: function () {
                    this.checkValue = true
                }
            },
            "value.aggregate": {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    if (Object.keys(newVal).length == 0) {
                        //delete agg

                    } else {
                        if (newVal.id == oldVal.id) {
                            // console.log("Equal")
                        } else {
                            this.checkValue = true
                            // console.log("no Equal")
                            var addObj = {
                                state: "addEvent",
                                element: JSON.parse(JSON.stringify(me.value))
                            }
                            var deleteObj = {
                                state: "deleteEvent",
                                element: JSON.parse(JSON.stringify(me.value))
                            }

                            if (oldVal.id != null) {
                                me.$EventBus.$emit(oldVal.id, deleteObj)
                            }
                            me.$EventBus.$emit(newVal.id, addObj)

                            //init agg entity
                            if (me.value.fieldDescriptors.length == 0) {
                                me.value.fieldDescriptors = JSON.parse(JSON.stringify(newVal.fieldDescriptors))
                            }

                        }
                    }

                }
            },
            "value.boundedContext": {
                deep: true,
                handler: function () {
                    this.checkValue = true
                }
            },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {

                    // if (me.checkValue) {

                    me.$nextTick(function () {

                        // me.setNameCase(me.namePanel)
                        var obj = {
                            state: "updateEvent",
                            element: JSON.parse(JSON.stringify(me.value))
                        }


                        //aggregate notify
                        if (me.value.aggregate.id != null) {
                            me.$EventBus.$emit(me.value.aggregate.id, obj)
                        }

                        //policy notify
                        if (me.value.relationPolicyInfo.length > 0) {
                            me.value.relationPolicyInfo.forEach(function (policy) {
                                console.log(policy)
                                me.$EventBus.$emit(policy.policyValue.elementView.id, obj)
                            })

                        }

                        // Command notiyfy
                        if (me.value.relationCommandInfo != undefined) {
                            if (me.value.relationCommandInfo.length > 0)
                                me.value.relationCommandInfo.forEach(function (command) {
                                    me.$EventBus.$emit(command.commandValue.elementView.id, obj)
                                })
                        }

                        me.checkValue = false
                    })

                    // }

                }

            },

        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {

                if (obj.state == 'deleteRelation') {

                    if (obj.element.targetElement._type == 'org.uengine.modeling.model.Policy') {
                        var targetId = obj.element.targetElement.elementView.id
                        me.value.relationPolicyInfo.some(function (policyItem, poIdx) {
                            if (policyItem.policyValue.elementView.id == targetId) {
                                me.value.relationPolicyInfo[poIdx] = null
                                return
                            }
                        })
                        me.value.relationPolicyInfo = me.value.relationPolicyInfo.filter(n => n)
                    } else {
                        var targetId = obj.element.targetElement.elementView.id
                        me.value.relationCommandInfo.some(function (commandItem, coIdx) {
                            if (commandItem.commandValue.elementView.id == targetId) {
                                me.value.relationCommandInfo[coIdx] = null
                                return
                            }
                        })
                        me.value.relationCommandInfo = me.value.relationCommandInfo.filter(n => n)
                    }


                } else if (obj.state == 'updateAggregate') {
                    console.log("updateAggregate")

                    me.value.aggregate.id = obj.element.elementView.id
                    me.value.aggregate.name = obj.element.name
                    me.value.aggregate.namePlural = obj.element.namePlural
                    me.value.aggregate.namePascalCase = obj.element.namePascalCase
                    me.value.aggregate.nameCamelCase = obj.element.nameCamelCase
                    me.value.aggregate.fieldDescriptors = obj.element.aggregateRoot.fieldDescriptors

                    // me.value.aggregate = tmpAgg
                } else if (obj.state == 'deleteAggregate') {
                    me.value.aggregate = {}
                } else if (obj.state == 'updateCommand') {
                    me.value.relationCommandInfo.some(function (policyItem, poIdx) {
                        if (policyItem.commandValue.elementView.id == obj.element.elementView.id) {
                            me.value.relationCommandInfo[poIdx].commandValue = obj.element
                            return
                        }
                    })
                } else if (obj.state == 'deleteCommand') {
                    var targetId = obj.element.elementView.id
                    me.value.relationCommandInfo.some(function (policyItem, poIdx) {
                        if (policyItem.commandValue.elementView.id == targetId) {
                            me.value.relationCommandInfo[poIdx] = null
                            return
                        }
                    })
                    me.value.relationCommandInfo = me.value.relationCommandInfo.filter(n => n)
                } else if (obj.state == 'updatePolicy') {
                    console.log(obj)
                    me.value.relationPolicyInfo.some(function (policyItem, poIdx) {
                        if (policyItem.policyValue.elementView.id == obj.element.elementView.id) {
                            me.value.relationPolicyInfo[poIdx].policyValue = obj.element
                            return
                        }
                    })
                } else if (obj.state == 'deletePolicy') {
                    var targetId = obj.element.elementView.id
                    me.value.relationPolicyInfo.some(function (policyItem, poIdx) {
                        if (policyItem.policyValue.elementView.id == targetId) {
                            me.value.relationPolicyInfo[poIdx] = null
                            return
                        }
                    })
                    me.value.relationPolicyInfo = me.value.relationPolicyInfo.filter(n => n)
                } else if (obj.state == 'updateBounded') {
                    console.log(obj)
                    me.value.boundedContext.name = obj.element.name;
                    me.value.boundedContext.id = obj.element.elementView.id;
                }

                if (obj.state == 'editUser') {
                    me.editUserImg.push(obj.picture)
                } else if (obj.state == 'closeEditUser') {
                    me.editUserImg.splice(me.editUserImg.indexOf(obj.picture), 1)
                }

            })
        },
        methods: {

            setNameCase:
                function (newVal) {
                    var me = this
                    me.value.name = newVal
                    me.value.namePascalCase = changeCase.pascalCase(newVal)
                    me.value.nameCamelCase = changeCase.camelCase(newVal)
                    me.value.namePlural = changeCase.camelCase(pluralize(newVal));
                },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },

            deleteEvent() {
                var me = this
                return new Promise(function (resolve) {

                    me.$EventBus.$off(`${me.value.elementView.id}`);

                    var obj = {
                        state: "deleteEvent",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.aggregate.id != null) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                    }
                    if (me.value.boundedContext.id != null) {
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)
                    }
                    //policy && relation notify
                    if (me.value.relationPolicyInfo.length > 0) {
                        me.value.relationPolicyInfo.forEach(function (poItem) {
                            //policy
                            me.$EventBus.$emit(`${poItem.policyValue.elementView.id}`, obj)
                            //relation
                            me.$EventBus.$emit(`${poItem.relationId}`, obj)
                        })
                    }

                    // var designer = me.getComponent('modeling-designer')
                    // console.log(designer.$refs[me.value.elementView.id] )
                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">
</style>
