<template>
    <v-card
            outlined
            max-width="500px"
    >
        <v-row align="center" justify="end" dense>
            <v-btn text small v-if="value.updateRules.length != 1"@click.native="removeMainRow(index)">
                <v-icon small>mdi-minus-circle-outline</v-icon>
            </v-btn>
        </v-row>

        <v-list-item-subtitle>
            <v-col align="start" justify="start" style="padding: 2px">
                <v-row align="start" justify="start" style=" margin-left: 10px; margin-right: 5px;">
                    <v-autocomplete
                            v-model="updateItem.when"
                            :items="eventList"
                            item-text="name"
                            return-object
                            prefix="UPDATE WHEN"
                            style="font-size: 15px"
                    ></v-autocomplete>
                </v-row>

                <div style="font-size: 15px; margin-left: 10px;">SET</div>
                <v-simple-table
                        dense style="margin-left: 10px;"
                >
                    <template v-slot:default>
                        <thead>
                        <tr>

                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="(item,key) in updateItem.fieldMapping">
                            <td class="text-right" v-if="key == updateItem.fieldMapping.length-1 && !saveBtnn">
                                <v-autocomplete
                                        v-model="item.viewField"
                                        :items="value.fieldDescriptors"
                                        item-text="name"
                                        return-object
                                        label="viewField"
                                ></v-autocomplete>
                            </td>
                            <td class="text-right" v-else> {{value.name}}.{{ item.viewField.name }}</td>

                            <td class="text-center"> =</td>

                            <td class="text-left" v-if="key == updateItem.fieldMapping.length-1 && !saveBtnn">
                                <v-autocomplete
                                        v-model="item.eventField"
                                        :items="syncEventField"
                                        item-text="name"
                                        return-object
                                        label="eventField"
                                ></v-autocomplete>
                            </td>
                            <td class="text-left" v-else> {{updateItem.when.name}}.{{ item.eventField.name }}</td>

                            <td class="text-center">
                                <v-btn small text
                                       @click.native="removeRow(updateItem.fieldMapping,key)">
                                    <v-icon small> delete</v-icon>
                                </v-btn>
                            </td>
                        </tr>
                        </tbody>
                    </template>
                </v-simple-table>

                <v-row align="center" justify="end" style="margin-left: 10px;">
                    <v-btn text small
                           @click.native="testt()">
                        <v-icon small v-if="saveBtnn"> mdi-pencil</v-icon>
                        <v-icon small v-else> mdi-content-save</v-icon>
                    </v-btn>
                    <v-btn text small @click.native="addRow(updateItem.fieldMapping)">
                        <v-icon small> mdi-plus</v-icon>
                    </v-btn>
                </v-row>


                <div style="font-size: 15px; margin-left: 10px;">WHERE</div>
                <v-simple-table
                        dense style="margin-left: 30px;"
                >
                    <template v-slot:default>
                        <thead>
                        <tr>

                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="(item,key) in updateItem.where">
                            <td class="text-right" v-if="key == updateItem.where.length-1  && !saveBtn">
                                <v-autocomplete
                                        v-model="item.viewField"
                                        :items="value.fieldDescriptors"
                                        item-text="name"
                                        return-object
                                        label="viewField"
                                ></v-autocomplete>
                            </td>
                            <td class="text-right" v-else> {{ value.name }}.{{ item.viewField.name }}</td>

                            <td class="text-center"> =</td>

                            <td class="text-left" v-if="key == updateItem.where.length-1  && !saveBtn">
                                <v-autocomplete
                                        v-model="item.eventField"
                                        :items="syncEventField"
                                        item-text="name"
                                        return-object
                                        label="eventField"
                                ></v-autocomplete>
                            </td>
                            <td class="text-left" v-else> {{ updateItem.when.name }}.{{ item.eventField.name}}</td>

                            <td class="text-center">
                                <v-btn small text
                                       @click.native="removeRow(updateItem.where,key)">
                                    <v-icon small> delete</v-icon>
                                </v-btn>
                            </td>
                        </tr>
                        </tbody>
                    </template>
                </v-simple-table>

                <v-row align="center" justify="end" style="margin-left: 10px;">
                    <v-btn text small
                           @click.native="test()">
                        <v-icon small v-if="saveBtn"> mdi-pencil</v-icon>
                        <v-icon small v-else> mdi-content-save</v-icon>
                    </v-btn>
                    <v-btn text small @click.native="addRow(updateItem.where)">
                        <v-icon small> mdi-plus</v-icon>
                    </v-btn>
                </v-row>

            </v-col>

        </v-list-item-subtitle>

    </v-card>
</template>

<script>
    export default {
        name: 'ViewUpdate',
        props: {
            value: Object,
            updateItem: Object,
            index: Number,
        },
        data: function () {
            return {
                saveBtnn: false,
                saveBtn: false,
            }
        },
        computed: {
            syncEventField: function () {

                if (Object.keys(this.updateItem.when).length > 0) {
                    if(this.eventList.findIndex(x => x.elementView.id === this.updateItem.when.elementView.id) != -1){
                        return this.eventList[this.eventList.findIndex(x => x.elementView.id === this.updateItem.when.elementView.id)].fieldDescriptors
                    }
                }
                return []
            },
            eventList: function () {
                var me = this
                var designer = me.getComponent('modeling-designer')

                var tmp = []

                designer.value.definition.forEach(function (tmpData, idx) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Event') {
                            tmp.push(tmpData)
                        }
                    }
                })

                tmp = tmp.filter(n => n)
                return tmp
            },
        },
        methods: {
            testt() {
                if (this.saveBtnn) {
                    this.saveBtnn = false
                } else {
                    this.saveBtnn = true
                }

            },
            test() {
                if (this.saveBtn) {
                    this.saveBtn = false
                } else {
                    this.saveBtn = true
                }

            },
            removeMainRow(viewId) {
                var me = this
                me.value.updateRules[viewId]= null
                me.value.updateRules =  me.value.updateRules.filter(n=>n)
            },
            removeRow: function (table, removeKey) {
                var me = this
                table.some(function (views, idx) {
                    if (idx == removeKey) {
                        if (idx > -1) {
                            table.splice(idx, 1)
                        }
                    }
                })
            },
            addRow: function (table) {
                var me = this

                if (table.length != 0 && (Object.keys(table[table.length - 1].viewField).length === 0 || Object.keys(table[table.length - 1].eventField).length === 0)) {
                    var designer = this.getComponent('modeling-designer')
                    designer.text = "TextField input Text"
                    designer.snackbar = true
                } else {
                    var obj = {
                        viewField: {},
                        eventField: {}
                    }
                    table.push(obj)
                }

            },
            removeConditionRow: function (table, removeKey) {
                var me = this
                table.some(function (views, idx) {
                    if (idx == removeKey) {
                        if (idx > -1) {
                            table.splice(idx, 1)
                        }
                    }
                })
            },
            addConditionRow: function (table) {
                var me = this


                if (table.length != 0 && (Object.keys(table[table.length - 1].conditionViewField).length === 0 || Object.keys(table[table.length - 1].conditionEventField).length === 0)) {
                    var designer = this.getComponent('modeling-designer')
                    designer.text = "TextField input Text"
                    designer.snackbar = true
                } else {
                    var obj = {
                        conditionViewField: {},
                        conditionEventField: {}
                    }
                    table.push(obj)
                }

                //
                // var obj = {
                //     conditionViewField: {},
                //     conditionEventField: {}
                // }
                // table.push(obj)

            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },
        }
    }
</script>