                <geometry-line
                        :from="[50,63]"
                        :to="[27,80]"
                ></geometry-line>
                <!--title-->
                <!--                <text-element-->
                <!--                        :sub-width="'100%'"-->
                <!--                        :sub-height="titleH"-->
                <!--                        :sub-top="0"-->
                <!--                        :sub-left="0"-->
                <!--                        :text="namePanel">-->
                <!--                </text-element>-->

            </sub-elements>
        </geometry-element>
        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                titleName="Actor"
