<script>
    import Element from '../../modeling/Element'
    import RectangleElement from "../../../opengraph/shape/RectangleElement";
    import CircleElement from "../../../opengraph/shape/CircleElement";
    import EdgeElement from "../../../opengraph/shape/EdgeElement";
    import EllipseElement from "../../../opengraph/shape/EllipseElement";
    import GeometryLine from "../../../opengraph/geometry/Line";
    import SubElements from "../../../opengraph/shape/SubElements";

    export default {
        components: {
            SubElements,
            GeometryLine,
            EllipseElement,
            EdgeElement,
            CircleElement,
            RectangleElement,
        },
        mixins: [Element],
        name: 'actor-definition',
        props: {},
        computed: {
            bodyHe() {
                return this.value.elementView.height
            },
            defaultStyle() {
                return {}
            },
            type() {
                return 'Actor'
            },
            className() {
                return 'org.uengine.modeling.model.Actor'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    innerAggregate: {
                        'event': [],
                        'command': [],
                        'view': [],
                        'policy': [],
                        'external': []
                    },
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 140,
                        'style': JSON.stringify({})
                    },
                    // drawer: false,
                    // selected: false,
                    boundedContext: ''
                }

            }
        },
        data: function () {
            return {
                itemH: 200,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference
            };
        },
        created: function () {
            var me = this
            this.$nextTick(function () {
                this.namePanel = me.value.name
            })
        },
//        watch: {
//            "value.name": function (newVal, oldVal) {
//                this.namePanel = newVal
//            },
//        },
        mounted: function () {

        },
        methods: {
            onPropertyUpdated() {
                console.log('atta')
                var me = this
                me.set
                this.value.name = me.namePanel
                // var value = this.value;
                // this.value = null;
                // this.value = value;
            },


            deleteActor() {
                var me = this
                return new Promise(function (resolve) {

                    me.$EventBus.$off(`${me.value.elementView.id}`);

                    var obj = {
                        state: "deleteActor",
                        element: JSON.parse(JSON.stringify(me.value))
                    }


                    me.$emit('update:value', null);
                    // me.$EventBus.$emit('storage')
                    resolve()
                })
            }
        }

    }
</script>


